import { TemplateComponent, definition } from '@eui/component';
import template from './appBar.html';
import style from './appBar.css';
import Config from '../../core/Config';
import { addSharedCSS, findElements } from '../../utils/General';
import { AppStore, Router } from '../../util-helpers';

import {
  MENU_TOGGLE_CLICKED,
  TOGGLE_NAV_MENU,
  CLOSE_NAV_MENU,
  ADD_APP_COMPONENTS,
  REMOVE_APP_COMPONENT,
  APP_LOADED,
  APP_LOADING,
  LOCALE_UPDATED,
  CANCEL_NAVIGATION,
} from '../../utils/Actions';

@definition('eui-app-bar', {
  style,
  props: {
    embedded: { attribute: true, type: Boolean },
  },
  template: addSharedCSS(template),
})
export default class AppBar extends TemplateComponent {
  /**
   * @function constructor
   * @description Constructor for the AppBar
   */
  constructor() {
    super();

    findElements(this, [
      { name: 'actionsHolder', selector: '.actions' },
      { name: 'appTitleContainer', selector: '#AppBar-title' },
      { name: 'subTitle', selector: '#Sub-title' },
      { name: 'subTitleTooltip', selector: '#Tooltip-description' },
      { name: 'leftContainer', selector: '.actions-left' },
      { name: 'rightContainer', selector: '#AppBar-right' },
      { name: 'menuToggle', selector: '#AppBar-menu-toggle' },
      { name: 'leftAnchor', selector: '#AppBar-menu-anchor' },
      { name: 'breadcrumb', selector: '#AppBar-breadcrumb' },
      { name: 'loader', selector: '#AppBar-loader' },
    ]);

    this._setInitialState();
    this._bindMethods();
    this._setupListeners();
  }

  didUpgrade() {
    if (this.embedded || Config.menu.hideMenu) {
      this._hideMenu();
    }
  }

  /**
   * @private
   * @function _setInitialState
   * @description Sets the initial state of the the AppBar
   */
  _setInitialState() {
    // Boolean state for the App Menu,
    // TODO, consider in the config, as with the System
    // or even in the store.
    this.isAppMenuOpen = false;
    this._activeAppData = null;
    this.leftAnchor.innerHTML = window.EUI.Localizer.loc.MENU_TITLE || 'Menu';
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this._handleMenuToggle = this._handleMenuToggle.bind(this);
    this._closeMenu = this._closeMenu.bind(this);
    this._addComponents = this._addComponents.bind(this);
    this._removeComponents = this._removeComponents.bind(this);
    this._onAppLoading = this._onAppLoading.bind(this);
    this._onAppLoaded = this._onAppLoaded.bind(this);
    this._onAppLocaleUpdated = this._onAppLocaleUpdated.bind(this);
    this._handleBreadCrumbClicks = this._handleBreadCrumbClicks.bind(this);
    this._cancelNavigation = this._cancelNavigation.bind(this);
  }

  /**
   * @private
   * @function _setupListeners
   * @description Sets up listeners
   */
  _setupListeners() {
    this.breadcrumb.addEventListener('eui-breadcrumb:change', this._handleBreadCrumbClicks, false);
    this.menuToggle.addEventListener('click', this._handleMenuToggle, false);
    this.leftAnchor.addEventListener('click', this._handleMenuToggle, false);

    AppStore.listen(MENU_TOGGLE_CLICKED, this._handleMenuToggle);
    AppStore.listen(CLOSE_NAV_MENU, this._closeMenu);
    AppStore.listen(ADD_APP_COMPONENTS, this._addComponents);
    AppStore.listen(REMOVE_APP_COMPONENT, this._removeComponents);
    AppStore.listen(APP_LOADING, this._onAppLoading);
    AppStore.listen(APP_LOADED, this._onAppLoaded);
    AppStore.listen(LOCALE_UPDATED, this._onAppLocaleUpdated);
    AppStore.listen(CANCEL_NAVIGATION, this._cancelNavigation);
  }

  /**
   * Event listener for CANCEL_NAVIGATION
   */
  _cancelNavigation() {
    // Breadcrumb is going to change its path no matter navigation has finished
    // or prevented, need to revert state when navigation being stopped.
    // eslint-disable-next-line no-undef
    requestAnimationFrame(() => {
      if (Config.showBreadcrumbs) {
        this.breadcrumb.data = this.breadcrumbSnapshot;
      }
    });
  }

  /**
   * @private
   * @function _hideMenu
   * @description hides the menu.
   */
   _hideMenu() {
    this.actionsHolder.classList.remove('open-menu');
    this.actionsHolder.classList.add('menu-hidden');
  }

  /**
   * @private
   * @function _addComponents
   * @description Adds components to the app bar
   * @param {Array} data - Data for the adding components to the app bar
   * @returns {Boolean} True, if components are added else false
   */
  _addComponents(data) {
    if (!data || typeof data !== 'object') {
      console.warn('AppBar: Unable to add components: %o to AppBar', data);
      return false;
    }

    if (!Array.isArray(data)) {
      data = [data];
    }

    let isAdded = false;
    try {
      isAdded = data.every((item) => {
        item.component.id = item.id;
        const componentNode = this.rightContainer.appendChild(item.component);
        if (componentNode) {
          return true;
        }
        return false;
      });
    } catch (e) {
      isAdded = false;
      console.warn('AppBar: Unable to add components: %o to AppBar, stack: %o', data, e);
    }
    return isAdded;
  }

  /**
   * @private
   * @function _removeComponents
   * @description Removes components from the app bar
   * @param {Object} data - Data for removing components from the App bar
   * @return {Boolean} True, if components are removed else false
   */
  _removeComponents(data) {
    if (!data || typeof data !== 'object') {
      console.warn('AppBar: Unable to remove components: %o from AppBar', data);
      return false;
    }

    if (!Array.isArray(data)) {
      data = [data];
    }

    let isRemoved = false;
    try {
      isRemoved = data.every((item) => {
        const componentNode = this.rightContainer.querySelector(`#${item.id}`);
        if (componentNode) {
          componentNode.remove();
          return true;
        }
        return false;
      });
    } catch (e) {
      isRemoved = false;
      console.warn('AppBar: Unable to remove components: %o from AppBar, stack: %o', data, e);
    }
    return isRemoved;
  }

  /**
   * @private
   * @function _closeMenu
   * @description Sets the menu to the closed state
   */
  _closeMenu() {
    this.isAppMenuOpen = false;
    this._toggleMenuState();
  }

  /**
   * @private
   * @function _handleMenuToggle
   * @description Handles the toggle event for the menu button
   */
  _handleMenuToggle(init) {
    if (Config.menu.hideMenu) {
      this.isAppMenuOpen = false;
    } else {
      this.isAppMenuOpen = (typeof init === 'boolean') ? init : !this.isAppMenuOpen;
    }
    AppStore.dispatch(TOGGLE_NAV_MENU, this.isAppMenuOpen);
    this._toggleMenuState();
  }

  /**
   * @private
   * @function _toggleMenuState
   * @description The method to switch the selectors for the menu items
   */
  _toggleMenuState() {
    if (this.isAppMenuOpen) {
      this.actionsHolder.classList.add('open-menu');
      return;
    }
    this.actionsHolder.classList.remove('open-menu');
  }

  /**
   * @private
   * @function _showBreadCrumbAfterAppIsLoaded
   * @description shows breadcrumb after the app is loaded
   */
  _showBreadCrumbAfterAppIsLoaded() {
    if (!Config.showBreadcrumbs || !this._activeAppData) {
      return;
    }

    // active app path can be single or with parents
    const { path: activeAppPath } = this._activeAppData.config.menu.data;

    // get all the app ids
    const appIds = activeAppPath.split('/');

    const elementsData = {};

    const activeAppId = appIds.pop();

    elementsData[activeAppId] = {
      label: window.EUI.Localizer.loc.MENU[this._activeAppData.config.menu.id],
    };

    // if single app
    if (appIds.length === 0) {
      this._setBreadcrumbData({
        path: activeAppPath,
        elements: elementsData,
      });
      return;
    }

    const getAppId = (appData) => {
      let packageId = appData.data.path;
      const pos = packageId.lastIndexOf('/');
      if (pos > -1) {
        packageId = packageId.substr(pos + 1);
      }

      return packageId;
    };

    const generateElementsLabels = (appItems) => {
      const parentAppId = appIds.shift();
      if (!parentAppId) return;

      for (let index = 0; index < appItems.length; index++) {
        const appItem = appItems[index];
        // If current app item is a group, add its children to the loop.
        if (appItem.type === 'group') {
          appItems = [...appItems, ...(appItem.children || [])];
          continue;
        }

        const match = parentAppId === getAppId(appItem);
        if (match) {
          elementsData[parentAppId] = { label: window.EUI.Localizer.loc.MENU[appItem.id] };
          generateElementsLabels(appItem.children);
          break;
        }
      }
    };

    // if multiple apps present
    generateElementsLabels(window.EUI.MenuConfig.app.menu.data);

    this._setBreadcrumbData({
      path: activeAppPath,
      elements: elementsData,
    });
  }

  /**
   * @private
   * @function _setBreadcrumbLoadingStatus
   * @description Set the loading status of loader component
   * @param {Boolean} state Status for loader
   */
  _setBreadcrumbLoadingStatus(state) {
    this.loader.hidden = !state;
  }

  /**
   * called when the locale is changed in app.
   * Re-generate the breadcrumb and localize any other strings.
   *
   * @function _onAppLocaleUpdated
   * @private
   */
  _onAppLocaleUpdated() {
    if (Config.showBreadcrumbs) {
      this._showBreadCrumbAfterAppIsLoaded();
    }
    this._switchStrings();
  }

  /**
   * @private
   * @function _onAppLoading
   * @description Handles the app loading event and clears the titles
   * @param {Object} packageName - object defining if it's a
   * sub app and the config file ({ packageName: 'app-3' })
   */
  _onAppLoading(packageName) {
    if (Router.getActiveAppId() === packageName) { return; }
    if (this.getBoundingClientRect().width < 480) {
      this._handleMenuToggle(false);
    }
    if (Config.showBreadcrumbs) {
      this._setBreadcrumbLoadingStatus(true);
      this.breadcrumb.data = null;
      return;
    }
    this.appTitleContainer.innerText = '';
    this.subTitle.textContent = '';
    this.subTitleTooltip.textContent = '';
  }

  /**
   * @private
   * @function _switchStrings
   * @description Switches App title strings
   */
  _switchStrings() {
    const { MENU, MENU_TITLE } = window.EUI.Localizer.loc;

    this.leftAnchor.innerHTML = MENU_TITLE || 'Menu';

    if (Config.showBreadcrumbs) {
      this._addSubtitle();
      return;
    }

    this.appTitleContainer.innerText = MENU[this._activeAppData.config.menu.id]
    || this._activeAppData.config.menu.id;
    this._addSubtitle();
  }

  /**
   * @private
   * @function _addSubtitle
   * @description Add subtitle to the app bar
   */
  _addSubtitle = () => {
    let subtitleName = '';
    if (this._activeAppData.config.subtitle) {
      subtitleName = window.EUI.Localizer.loc[this._activeAppData.config.subtitle]
      || this._activeAppData.config.subtitle;
    }
    this.subTitle.textContent = subtitleName;
    this.subTitleTooltip.textContent = subtitleName;
  }

  /**
   * @private
   * @function _onAppLoaded
   * @description Handles the app loaded event and sets the new titles
   * @param {Object} data - App data
   */
  _onAppLoaded(data) {
    if (data) {
      this._activeAppData = data;
      AppStore.setState('activeAppPath', this._activeAppData.config.menu.data.path);
    }

    if (this._activeAppData.config.menu.contextualBreadcrumb === true) {
      // Inject $appBar tp loaded app instance when app has contextual breadcrumb
      const loadedApp = data.app || data.instance;
      this._injectAppBar(loadedApp, () => this._setBreadcrumbLoadingStatus(false));
    } else {
      this._showBreadCrumbAfterAppIsLoaded();
      this._setBreadcrumbLoadingStatus(false);
    }

    this._switchStrings();
  }

  /**
   * Set data to app bar breadcrumb
   * @param {Object} data - Data set to breadcrumb
   */
  _setBreadcrumbData(data) {
    this.breadcrumb.data = data;
    this.breadcrumbSnapshot = JSON.parse(JSON.stringify(data));
  }

  /**
   * @private
   * @function _injectAppBar
   * @description Inject $appBar to loaded app instance
   * @param {Object} app loaded app
   * @param {Function} callback needs to be executed after rendering contextual breadcrumb
   */
  _injectAppBar(app, callback) {
    app.$appBar = {
      renderBreadcrumbWithContext: (elements) => {
        // Stop rendering when it's configured not to show breadcrumb
        if (!Config.showBreadcrumbs) { return; }

        const elementsData = {};
        const pathSegments = [];
        for (const item of elements) {
          const path = item.pathSegment;
          pathSegments.push(path);
          elementsData[path] = {
            label: item.contextualTitle || window.EUI.Localizer.loc.MENU[item.appId],
            contextualPath: item.contextualPath,
          };
        }

        this._setBreadcrumbData({
          path: pathSegments.join('/'),
          elements: elementsData,
        });
        callback();
      },
    };
  }

  /**
   * @private
   * @function _handleBreadCrumbClicks
   * @description Handles clicks on the breadcrumbs
   * @param {Object} e - Event object
   */
  _handleBreadCrumbClicks(e) {
    const { activeAppPath } = e.detail;
    Router.goto(activeAppPath);
  }
}
