import { TemplateComponent, definition } from '@eui/component';
import template from './appNav.html';
import style from './appNav.css';
import { addSharedCSS, findElements } from '../../utils/General';
import Config from '../../core/Config';
import Functions from '../../utils/Functions';
import { AppStore } from '../../util-helpers';
import {
  LEFT_PANEL_LOADING,
  LEFT_PACKAGE_LOADED,
  REMOVE_LEFT_PANEL,
} from '../../utils/Actions';

@definition('eui-app-nav', {
  style,
  template: addSharedCSS(template),
})
export default class AppNav extends TemplateComponent {
  /**
   * @function constructor
   * @description Constructor for the AppNav
   */
  constructor() {
    super();

    findElements(this, [
      { name: 'container', selector: '#AppNav-inner' },
      { name: 'loaderComponent', selector: '.loader' },
    ]);

    this.menuPanelTag = '';

    this._bindMethods();
    this._setupListeners();
    this._loadAppNav();
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this.loader = this.loader.bind(this);
    this._attachPanel = this._attachPanel.bind(this);
  }

  /**
   * @private
   * @function _setupListeners
   * @description Sets up listeners
   */
  _setupListeners() {
    AppStore.listen(LEFT_PANEL_LOADING, this.loader);
    AppStore.listen(LEFT_PACKAGE_LOADED, this._attachPanel);
    AppStore.listen(REMOVE_LEFT_PANEL, this._removePanel);
  }

  /**
   * @private
   * @function _loadAppNav
   * @description Loads the App navigation
   */
  _loadAppNav() {
    const { ui } = Config;
    if (!ui || typeof ui !== 'object' || Array.isArray(ui)) {
      console.warn('AppNav: Unabe to load Apps Navigation with \'ui\' config: %o', ui);
      return;
    }

    const { left: leftComponent } = ui;
    if (!leftComponent || typeof leftComponent !== 'object' || Array.isArray(leftComponent)) {
      console.warn('AppNav: Unabe to load Apps Navigation with \'ui.left\' config: %o', leftComponent);
      return;
    }

    Functions.loadPackage({
      packageName: leftComponent.name,
      type: 'left',
      localeInfo: {
        localeToLoad: AppStore.getState('locale'),
        localeIds: Config.i18n.localeIds,
      },
      path: Config.panel.panelsPath,
    });
  }

  /**
   * @private
   * @function _attachPanel
   * @description Attach the panel to the container
   * @param {Object} panel - The panel instance to inject into the dom
   */
  _attachPanel(panel) {
    this.menuPanelTag = panel.constructor.is;
    this.container.appendChild(panel);
    this.loader(false);
  }

  /**
   * @private
   * @function _removePanel
   * @description Removes the current loaded panel from the display
   * @param {Object} panel - The app to remove from the layer
   */
  _removePanel(panel) {
    this.menuPanelTag = '';
    panel.remove();
  }

  /**
   * @public
   * @function loader
   * @description Toggles the loader component in the App navigation
   * @param {Boolean} display - True/False to show or hide the loader
   */
  loader(display = true) {
    this.container.className = display ? '' : 'hideloader';
    this.loaderComponent.hidden = !display;
  }
}
