/* global SystemJS */
/* eslint prefer-promise-reject-errors:0 */

/**
 * @class Plugins
 * @description Handles any plugins loaded into the container
 */
class Plugins {
  /**
   * @function constructor
   * @description The constructor for the plugin object
   */
  constructor() {
    this.objs = {};
    this._pluginsTimeouts = {};
    this._pluginTimeout = 500;

    this._configureSystemJS();
  }

  /**
   * @private
   * @function _configureSystemJS
   * @description Configures the SystemJS
   */
  _configureSystemJS() {
    SystemJS.config({
      meta: {
        '*': { format: 'amd' },
      },
    });
  }

  /**
   * @function setPluginTimeout
   * @description Sets the timeout for the plugin
   * @param {Number} timeout - timeout in milliseconds
   */
  setPluginTimeout(timeout) {
    if (!timeout) {
      return;
    }
    this._pluginTimeout = timeout;
  }

  /**
   * @function load
   * @description Loads any required plugins into the system
   * @param {String} path - path of plugins dir
   * @param {Array} plugins - Single plugin or array of plugins to load
   * @param {function} callback - Function to be called when the plugins are loaded
   */
  async load(path, plugins, callback) {
    if (!plugins) {
      console.warn('Plugins: Missing plugins: %o to load', plugins);
      return;
    }
    if (!Array.isArray(plugins)) {
      plugins = [plugins];
    }
    const pluginPath = path || 'plugins/';
    const loadPlugin = async (plugin) => {
      try {
        const mod = await SystemJS.import(`${pluginPath}${plugin.name}/index.js${window.EUI_TS ? `?${window.EUI_TS}` : ''}`);
        this.objs[plugin.name] = mod.default || mod;
      } catch (e) {
        console.warn('Plugins: Unable to load the plugin: %o, error: %o', plugin.name, e);
      }
    };

    await Promise.all(plugins.map(loadPlugin));

    if (typeof callback === 'function') {
      callback();
    }
  }

  /**
   * @function find
   * @description Finds a plugin and executes a specific method on it, plugins may
   * or may not return a value or execute a callback
   * @param {String} name - The name of the plugin
   * @param {String} method - The method name to execute on the plugin
   * @param {Object} params - The optional parameters to pass to the plugin
   */
  find(name, method, params) {
    const warningMsg = 'Plugins: Couldn\'t find the plugin: %o or the method: %o in it';
    if (!name || !method) {
      console.warn(warningMsg, name, method);
      return null;
    }
    const { objs } = this;
    if (!objs[name] || typeof objs[name][method] !== 'function') {
      console.warn(warningMsg, name, method);
      return null;
    }
    return this.objs[name][method](params);
  }

  /**
   * @function executeAll
   * @description Execute optional lifecycle hooks (onBeforeContainerLoad | onBeforeAppLoad)
   * on loaded plugins.
   * @param {String} method - name of the function to execute
   * @param {Any} params - parameters to pass to the function
   * @return Promise
   * @protected
   */
  executeAll(method, params) {
    const hooks = [];
    Object.keys(this.objs).forEach((pluginName) => {
      if (this.objs[pluginName] && typeof this.objs[pluginName][method] === 'function') {
        const data = { pluginName, method, params };
        const promises = [
          this._timeoutPlugin(data),
          this._executePluginHook(data),
        ];
        hooks.push(Promise.race(promises));
      }
    });
    return Promise.all(hooks);
  }

  /**
   * @function _timeoutPlugin
   * @description Sets the plugin's execution timeout
   * @param {String} pluginName - name of the plugin
   * @param {String} method - name of the lifecycle hook
   * @param {Any} params - parameters to pass to the lifecycle hook
   * @private
   */
  _timeoutPlugin(data) {
    return new Promise((resolve, reject) => {
      this._pluginsTimeouts[data.pluginName] = setTimeout(() => {
        if (data.method === 'onBeforeContainerLoad') {
          console.warn('Plugins: Failed to resolve plugin: %o', data);
          resolve();
          return;
        }
        console.warn('Plugins: Failed to execute plugin: %o', data);
        reject(`Plugin: ${data.pluginName}, failed to resolve`);
      }, this._pluginTimeout);
    });
  }

  /**
   * @function _getWindow
   * @description returns property on the global window object
   * @param {String} - Property name
   */
  _getWindow(prop) {
    return window[prop];
  }

  /**
   * @function _executePluginHook
   * @description Executes the plugin hook
   * @param {Object} - Data for executing plugin hook
   * @private
   */
  _executePluginHook(data) {
    const { pluginName, method, params } = data;
    const lifecycleHook = this.objs[pluginName][method];
    if (!lifecycleHook) { return null; }
    const hook = lifecycleHook(params);
    return new Promise((resolve, reject) => {
      hook(resolve, reject);
    }).catch((error) => {
      const EUI = this._getWindow('EUI');
      if (EUI.Router.getActiveAppId() !== params.appName) { return; }
      console.warn('Plugins: Plugin failure: %o\nerror: %o', data, error);
      if (data.method === 'onBeforeAppLoad') {
        throw error;
      }
    }).finally(() => {
      clearTimeout(this._pluginsTimeouts[pluginName]);
    });
  }
}

const obj = new Plugins();
export default obj;
