/* eslint no-use-before-define:0 */
import EuiCache from '../core/Cache';
import {
  PackageLoader,
  AppStore,
  Plugins,
  Load,
  Router,
} from '../util-helpers';

import {
  APP_LOADING,
  APP_LOADING_FAILED,
  LEFT_PANEL_LOADING,
  LEFT_PACKAGE_LOADED,
  RIGHT_PANEL_LOADING,
  RIGHT_PACKAGE_LOADED,
  APP_LOADED,
  UPDATE_LOCALE,
  CANCEL_NAVIGATION,
} from './Actions';

let lastAppLoadTimestamp;

/**
 * @function executePackageLifecycle
 * @description Executes an internal lifecycle call for the package
 * @param {Object} packageObj - The package to execute the command on
 * @param {String} methodName - The command name to execute onPause, onResume
 * @param {Object} args - Any arguments to pass to the method
 */
const executePackageLifecycle = (packageObj, methodName, args) => {
  if (!packageObj || typeof packageObj !== 'object' || Array.isArray(packageObj)
    || typeof methodName !== 'string' || !packageObj[methodName]
    || typeof packageObj[methodName] !== 'function') {
    return null;
  }

  return packageObj[methodName](args || []);
};

/**
 * @function getActionConstants
 * @description Gets the action constants depending on the type
 * @param {String} type - Type of the package
 * @param {String} packageName - Name of the package
 * @returns {Object} Action constants depending on the type {actionLoading, actionLoaded}
 */
const getActionConstants = (type) => {
  let actionLoading = null;
  let actionLoaded = null;
  switch (type) {
    case 'left':
      actionLoading = LEFT_PANEL_LOADING;
      actionLoaded = LEFT_PACKAGE_LOADED;
      break;
    case 'right':
      actionLoading = RIGHT_PANEL_LOADING;
      actionLoaded = RIGHT_PACKAGE_LOADED;
      break;
    default:
      break;
  }
  return { actionLoading, actionLoaded };
};

/**
 * @function _setComponentProps
 * @description Sets the props on the component
 * @param {Object} component - Component to set props on
 * @param {Object} props - Props to set on the component
 */
const _setComponentProps = (component, props) => {
  if (!component || !props || typeof component !== 'object' || Array.isArray(component)
    || typeof props !== 'object' || Array.isArray(props)) {
    console.warn('Functions: Unable to set props: %o on component: %o', props, component);
    return;
  }

  Object.entries(props).forEach(([key, value]) => {
    component[key] = value;
  });
};

/**
 * @function setProps
 * @description Sets the props in the data on the component
 * @param {Object} component - The component to set the props for
 * @param {Object} data - The query and rest data props
 * @param {Object} data.query - query passed using URL
 * @param {Object} data.options - options passed
 */
const setProps = (component, data) => {
  if (typeof data !== 'object' || Array.isArray(data)) {
    console.warn('Functions: Invalid props: %o for component: %o', data, component);
    return;
  }

  const { query, options } = data;
  _setComponentProps(component, query);
  _setComponentProps(component, options);
};

/**
 * @description Dispatches an event when app has finished loading
 * @param {Object} data - Data to dispatch
 * @param {String} data.packageName - Name of the package
 * @param {Object} data.packageConfig - Config of the package
 */
const dispatchAppLoaded = (data) => {
  const { packageName, packageConfig, app } = data;
  AppStore.dispatch(APP_LOADED, {
    appId: packageName,
    config: packageConfig,
    app,
  });
};

/**
 * @function notifyContainer
 * @description Notifies container of an event
 * @param {Object} data Data to pass the container
 * @param {String} data.type - Type of the package
 * @param {Object} data.cache - Cached package
 * @param {String} data.packageName - Name of the package
 * @param {String} data.actionName - Action to dispatch
 */
const notifyContainer = (data) => {
  const {
    type,
    cache,
    packageName,
    actionName,
  } = data;

  const { instance, config: packageConfig } = cache;

  executePackageLifecycle(instance, 'onResume');

  // Dispatch loaded action so that the container can inject the instance
  if (type !== 'app') {
    AppStore.dispatch(actionName, instance);
    return;
  }

  // Dispatch loaded action for app
  dispatchAppLoaded({
    packageName,
    packageConfig,
    app: instance,
  });
};

/**
 * @function packageIsCached
 * @description Updates the container environment of the cached package
 * @param {Object} data Data to pass the container
 * @param {String} data.type - type of the package
 * @param {Object} data.cache - Cached package
 * @param {String} data.packageName - Name of the package
 * @param {String} data.actionName - Action name to dispatch
 * @param {String} data.localeInfo - Locale info
 * @param {Object} data.locationParams Location parameters
 */
const packageIsCached = (data) => {
  const {
    type,
    cache,
    packageName,
    actionName,
    localeInfo,
    locationParams,
  } = data;

  if (locationParams) {
    setProps(cache.instance, locationParams);
  }

  AppStore.dispatch(UPDATE_LOCALE, {
    localeId: AppStore.getState('locale'),
    callback: () => {
      EuiCache.setCurrent(type, packageName);
      notifyContainer({
        type,
        cache,
        packageName,
        actionName,
      });
    },
    context: { [type]: { id: packageName } },
    prevContext: { id: EuiCache.getCurrent(type) },
    localeInfo,
  });
};

/**
 * @function resumeAppDisconnect
 * @description continues the loading of the new app
 */
function resumeAppDisconnect() {
  this.forcedDisconnect = true;
  this.resumeAppDisconnect.next();
}

/**
 * @function initPackage
 * @description Initializes the package class and stores in the cache
 * @param {Object} data Data for initializing the package
 * @param {String} data.type The type of package whether, left, app or right
 * @param {String} data.packageName The name of the package
 * @param {Object} data.locationParams Location parameters
 * @param {Object} data.packageDetails The package object
 * @param {Boolean} isCurrent true, if its a current package else false, default is true
 * @returns {Object} Package instance
 */
const initPackage = (data, isCurrent = true) => {
  if (!data || typeof data !== 'object' || Array.isArray(data)) {
    console.warn('Functions: Invalid data: %o provided to initialize package', data);
    return null;
  }

  const {
    type,
    packageName,
    locationParams,
    packageDetails,
  } = data;

  if (typeof type !== 'string' || type === '' || typeof packageName !== 'string' || packageName === '') {
    console.warn('Functions: Unable to initialize package: %o of type: %o', packageName, type);
    return null;
  }

  if (!packageDetails || typeof packageDetails !== 'object' || Array.isArray(packageDetails)) {
    console.warn('Functions: Unable to initialize package: %o with name: %o', packageDetails, packageName);
    return null;
  }

  const { module, config } = packageDetails;
  const PackageClass = (typeof module !== 'function') ? module.default : module;

  // Registers the PackageClass with the Custom elements registry
  if (PackageClass.register) { PackageClass.register(); }

  const packageInstance = new PackageClass();
  packageInstance.config = config;
  packageInstance.provider = {
    plugins: Plugins,
    api: Load,
    store: AppStore,
  };
  packageInstance.resumeAppDisconnect = resumeAppDisconnect.bind(packageInstance);
  executePackageLifecycle(packageInstance, 'onResume');

  if (locationParams) {
    setProps(packageInstance, locationParams);
  }

  EuiCache.storePackage({
    type,
    packageName,
    packageInstance,
    config,
    isCurrentPackage: isCurrent,
    locationParams,
  });
  return packageInstance;
};

/**
 * @function _packageLoadingSuccess
 * @description Dispatch an action when package has loaded successfully
 * @param {String} data.packageName The name of the package
 * @param {String} data.type The type of package whether, left, app or right
 * @param {String} data.actionName Name of the action to emit
 * @param {Object} data.locationParams Location parameters
 * @param {Object} data.packageDetails Details about the package
 * @param {Object} data.startTime Details about time of start of load process
 */
const _packageLoadingSuccess = (data) => {
  const {
    packageName,
    type,
    actionName,
    locationParams,
    packageDetails,
    startTime,
  } = data;

  // Instantiate and cache the package
  const packageObj = initPackage({
    type,
    packageName,
    packageDetails,
    locationParams,
  });

  // Dispatch the loaded action so that the correct container can insert the component
  if (type !== 'app') {
    AppStore.dispatch(actionName, packageObj);
    return;
  }

  // Check if App to be loaded is still valid, another process maybe running in parallel
  // Resolve only last one user triggers, others can be cached for fast loading later
  if (lastAppLoadTimestamp === startTime) {
    dispatchAppLoaded({
      packageName,
      packageConfig: packageDetails.config,
      app: packageObj,
    });
  }
};

/**
 * @function _packageLoadingFailed
 * @param {Object} e - Error object
 * @param {String} type - Type of package
 */
const _packageLoadingFailed = (e, type, packageName) => {
  console.warn('Functions: Unable to load package of type: %o stack: %o', type, e);

  // NOTE: Because the app failed to load the current instance was not cleared
  // in the cache for the previous app loaded, thus clear.
  EuiCache.clearCurrent(type);

  // TODO: What to do if left or right panels, at the moment the same functionality
  // was preserved and the preloader will simply hang.
  if (type === 'app') {
    const stack = e.stack.replace(new RegExp(`^ ${e}`), '');
    const content = `${e} + '\n' + ${stack}`;

    const data = {
      errorData: {
        header: `Failed to load app: ${packageName}`,
        content,
      },
    };

    AppStore.dispatch(APP_LOADING_FAILED, data);
  }
};

const pauseCurrentPackage = (type) => {
  const currentInstance = EuiCache.getCurrentInstance(type);
  executePackageLifecycle(currentInstance, 'onPause');
};

/**
 * @function loadPackage
 * @description Executes the request to load or retrieve a cached package
 * @param {String} data.packageName The name of the package
 * @param {String} data.type The type of package whether, left, app or right
 * @param {Object} data.localeInfo Locale object containing the available
 * locales and the current set
 * @param {Object} data.locationParams Location parameters
 * @param {String} data.path - path of packages
 */
const loadPackage = (data) => {
  const {
    packageName,
    type,
    localeInfo,
    locationParams,
    path,
  } = data;

  // const { actionLoading, actionLoaded } = getActionConstants(type, packageName);
  const { actionLoading, actionLoaded } = getActionConstants(type);

  // Notify all the listeners of panel loading
  if (type !== 'app') {
    AppStore.dispatch(actionLoading, data);
    pauseCurrentPackage(type);
  }

  // Check the cache to determine if an instance was previously loaded
  const cache = EuiCache.isPackageCached(packageName);
  if (cache) {
    packageIsCached({
      type,
      cache,
      packageName,
      actionName: actionLoaded,
      localeInfo,
      locationParams,
    });
    return null;
  }

  const startTime = new Date();
  lastAppLoadTimestamp = startTime;
  // No cache reference has been found and thus utilize a promise to
  //  load the package using the package loader
  return PackageLoader.load({
    packageName,
    path,
    localeInfo,
    prevContext: { id: EuiCache.getCurrent(type) },
  }).then((packageDetails) => {
    _packageLoadingSuccess({
      packageName,
      type,
      actionName: actionLoaded,
      locationParams,
      packageDetails,
      startTime,
    });
  }).catch((e) => {
    _packageLoadingFailed(e, type, packageName);
  });
};

/**
 * @function continueAppLoading
 * @description Completes the loading of the new app
 * @param {Object} data Data about the app to be loaded
 */
const continueAppLoading = (data) => {
  const { packageName } = data;
  AppStore.dispatch(APP_LOADING, { packageName });
  loadApp.activeApp = packageName;
  return Plugins.executeAll('onBeforeAppLoad', {
    appName: packageName,
    path: data.path,
    store: AppStore,
  }).then(() => {
    if (Router.getActiveAppId() !== packageName) { return null; }
    return loadPackage(data);
  }).catch((error) => {
    if (Router.getActiveAppId() !== packageName) { return; }
    const _data = {
      packageName,
      errorData: { header: `Failed to load app: ${packageName}`, content: error },
    };
    AppStore.dispatch(APP_LOADING_FAILED, _data);
    console.warn(`Functions: Failed to load app: ${packageName}, error: %o`, error);
  });
};

/**
 * Get the canceling navigation path
 * @returns path - canceling navigation path
 */
const getRealPath = () => {
  const currentApp = EuiCache.isPackageCached(loadApp.activeApp);
  const {
    config,
    locationParams: { query, options },
  } = currentApp;
  let { menu: { data: { path } } } = config;
  if (query && Object.keys(query).length > 0) {
    path += `?${Object.entries(query)
      .map(([key, val]) => `${key}=${val}`)
      .join('&')}`;
  } else if (options && Object.keys(options).length > 0) {
    path += `/${Object.entries(options)
      .map(([key, val]) => `!${key}/${val}`)
      .join('/')}`;
  }

  return path;
};

/**
 * Executes the request to load an APP. onBeforeAppLoad plugin lifecycle
 * hook is called on all loaded plugins.
 *
 * @function loadApp
 * @param {Object} data Data about parent app
 * @param {String} data.path Path to the packages folder
 * @param {String} data.packageName Name of parent package
 * @param {Object} data.localeInfo Locale object containing current locale and
 * the available locales
 * @param {Object} data.locationParams Location parameters
 */
const loadApp = (data) => {
  if (!loadApp.activeApp) {
    loadApp.activeApp = null;
  }

  const { packageName } = data;

  if (loadApp.activeApp === packageName) {
    const cache = EuiCache.isPackageCached(packageName);
    if (cache) {
      setProps(cache.instance, data.locationParams);
    }
    return null;
  }

  const currentInstance = EuiCache.getCurrentInstance('app');
  if (currentInstance && !currentInstance.forcedDisconnect) {
    const disconnectSignal = executePackageLifecycle(currentInstance, 'shouldAppDisconnect');
    if (!disconnectSignal) {
      const toUrl = new URL(window.location.href);
      currentInstance.resumeAppDisconnect.next = () => {
        const gotoPath = Router.mode === 'hash' ? toUrl.hash.replace(/#/, '') : toUrl.pathname;
        Router.goto(gotoPath);
        // Clean the force continuing disconnect loaded app in next tick
        setTimeout(() => {
          currentInstance.forcedDisconnect = false;
        }, 0);
      };

      Router.goto(getRealPath());
      AppStore.dispatch(CANCEL_NAVIGATION);

      return null;
    }
  }

  pauseCurrentPackage('app');
  return continueAppLoading(data);
};

export default {
  loadPackage,
  loadApp,
};
