import Load from './PromiseLoader';

const localesFileCache = new Map();
const localesCache = new Map();
let _loc = {};
let defaultLocale = '';

/**
 * Get the localized version of the inputted String.
 * If the localesCache is not setup, return null which will allow the
 * developer to set their alternative String as per documentation
 *
 * @function getString
 * @param {String} prop - name of string to localize
 * @private
 */
function getString(prop) {
  let _string = _loc[prop];
  if (!_string) {
    const _localesCache = localesCache.get(defaultLocale) || localesCache.get('en-us');
    if (_localesCache == null) {
      return prop;
    }
    _string = _localesCache[prop];
    if (!_string) {
      _string = localesCache.get('en-us')[prop];
    }
  }
  return _string;
}

/**
 * @class Localizer
 * @description  API for i18n
 */
class Localizer {
  /**
   * @function constructor
   * @description The constructor for the i18n API
   */
  constructor() {
    // Default options for the cache
    this.localesType = 'file';
    this.defaultDateLocale = 'en-us';

    this.loc = new Proxy({}, {
      get(obj, prop) {
        return getString(prop);
      },
    });

    this._bindMethods();
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to this context
   */
  _bindMethods() {
    this.load = this.load.bind(this);
    this.purge = this.purge.bind(this);
    this.switchLocale = this.switchLocale.bind(this);
  }

  /**
   * @private
   * @function _getLocalesFileCache
   * @description Returns information about the loaded components locales files
   * @returns {Object} locales file cache
   */
  _getLocalesFileCache() {
    return localesFileCache;
  }

  /**
   * @private
   * @function _getLocalesCache
   * @description Returns locale strings of the loaded components
   * @returns {Object} locales cache
   */
  _getLocalesCache() {
    return localesCache;
  }

  /**
   * @private
   * @function _getLocaleStrings
   * @description Returns active cache of locales
   * @returns {Object} locales cache
   */
  _getLocaleStrings() {
    return _loc;
  }

  /**
   * @public
   * @function setOptions
   * @description Sets the options{ localesType, defaultDateLocale, .. } for the i18n API
   * @param {Object} config - The config object
   */
  setOptions(config) {
    if (!config || typeof config !== 'object' || Array.isArray(config)) {
      console.warn('Localizer: Invalid options received for locale cache: %o', config);
      return;
    }
    Object.keys(config).forEach((key) => {
      this[key] = config[key];
      if (key === 'defaultLocale') {
        defaultLocale = config[key];
      }
    });
  }

  /**
   * @public
   * @function load
   * @description Loads a locale file into the cache
   * @param {Object} data Locale data
   * @param {Object} data.localeInfo - Info about the locale
   * @param {String} data.contextId - Id of the context - 'default', 'app1'..
   * @param {String} data.prevContext - Info about the previous context
   * @param {Function} callback Optional callback function
   */
  async load(data, callback) {
    if (!data || typeof data !== 'object' || Array.isArray(data)) {
      console.warn('Localizer: Invalid locale data provided: %o to load', data);
      return;
    }

    const { localeInfo, contextId, prevContext } = data;
    if (!contextId || typeof contextId !== 'string') {
      console.warn('Localizer: Invalid contextId: %o provided for loading locales', contextId);
      return;
    }

    if (prevContext) {
      this._deletePrevContextStrings(prevContext);
    }

    if (!localeInfo) {
      return;
    }

    // Check the file cache to determine if the files
    // have already been passed for this section
    let cache = localesFileCache.get(contextId);
    if (!cache) {
      const localesFileInfo = this._getLocalesFileInfo(localeInfo, contextId);

      if (!localesFileInfo || Array.isArray(localesFileInfo)) {
        console.warn('Localizer: Invalid localesData: %o provided to update file cache', localesFileInfo);
        return;
      }
      localesFileCache.set(contextId, localesFileInfo);
      cache = localesFileInfo;
    }
    const { localeToLoad } = localeInfo;

    try {
      await this._loadLocale(localeToLoad, cache[localeToLoad]);
      _loc = localesCache.get(localeToLoad) || {};
    } catch (errorOne) {
      console.warn(`Could not find locale for ${localeToLoad}, trying ${defaultLocale}...
      STACK: ${errorOne.message}`);
      try {
        await this._loadLocale(defaultLocale, cache[defaultLocale]);
        _loc = localesCache.get(defaultLocale) || {};
      } catch (errorTwo) {
        console.warn(`Could not find locale for ${defaultLocale}, trying en-us...
        STACK: ${errorTwo.message}`);
        try {
          await this._loadLocale('en-us', cache['en-us']);
          _loc = localesCache.get('en-us') || {};
        } catch (errorThree) {
          console.error(`Could not find locale for en-us STACK: ${errorTwo.message}`);
          _loc = {};
        }
      }
    }
    if (typeof callback === 'function') {
      callback();
    }
  }

  /**
   * @private
   * @function _getLocalesFileInfo
   * @description Information about locales files like path, loaded state etc...
   * Use default localesPath if the localesPath is not set.
   * Use default localeIds if the localeIds are not set.
   *
   * @param {Object} data - Locale data
   * @param {String} contextId - id of the component
   * @returns {Object} - Info about locales files
   */
  _getLocalesFileInfo(data, contextId) {
    let { localesPath, localeIds } = data;

    if (localesPath === undefined) {
      ({ localesPath } = this);
      console.info(`Localizer: using default locale location path (${localesPath}) for ${contextId}`);
    }

    if (localeIds === undefined) {
      ({ localeIds } = this);
      console.info(`Localizer: using default localeIds (%o) for ${contextId}`, localeIds);
    }

    if (!localesPath || typeof localesPath !== 'string') {
      console.warn('Localizer: Invalid localesPath: %o provided', localesPath);
      return null;
    }

    if (!localeIds || !Array.isArray(localeIds)) {
      console.warn('Localizer: Invalid localesList: %o were provided to load', localeIds);
      return null;
    }

    // stores the locale path and its loaded state
    // for each reference('default', component or app)
    const localesFileInfo = {};
    localeIds.forEach((localeId) => {
      localesFileInfo[localeId] = this._setLocaleFileInfo(localeId, localesPath);
    });
    return localesFileInfo;
  }

  /**
   * @private
   * @function _setLocaleFileInfo
   * @description Sets the locale file info {locale path, loaded state..}
   * @param {String} localeId - Id of the locale
   * @param {String} localesPath - Path of the locale file
   */
  _setLocaleFileInfo(localeId, localesPath) {
    if (typeof localeId !== 'string' || localeId === '') {
      console.warn('Localizer: expected locale: %o to be string', localeId);
      return null;
    }

    let localePath = `${localesPath}${localeId}`;
    if (this.localesType === 'file') {
      localePath = `${localePath}.json`;
    }
    return { path: localePath };
  }

  /**
   * @private
   * @function _loadLocale
   * @description Loads a single locale file into the cache
   * @param {String} localeName - The locale name i.e. en-gb
   * @param {Object} localeFileInfo - The locale file info { path:'', loaded: false }
   * @param {String} localeFileInfo.path - Locale path
   */
  async _loadLocale(localeName, localeFileInfo) {
    // Execute the fetch request to return the json file,
    // TODO The path may actually be an absolute API reference, to which event
    // There needs to be a mechanism to append the request value i.e
    // https://server.com/locale?type=en-gb
    // OR https://server.com/locale/en-gb
    if (!localeName || !localeFileInfo || typeof localeName !== 'string'
      || typeof localeFileInfo !== 'object' || Array.isArray(localeFileInfo)) {
      console.warn('Localizer: Invalid locale: %o and file: %o references provided', localeName, localeFileInfo);
      throw new Error(`Invalid locale: ${localeName} and file references provided`);
    }

    const { path: localePath } = localeFileInfo;
    if (!localePath) {
      console.warn('Localizer: Invalid locale path: %o', localePath);
      throw new Error(`Localizer: Invalid locale path: ${localePath}`);
    }

    // load locale file
    const json = await Load.json(localePath);
    if (!json) {
      console.warn('Localizer: Invalid locale: %o data returned', json);
      throw new Error(`Invalid locale: ${localeName}`);
    }

    // Either key values are stored in a ".values" property or
    // they are at the root of the json
    const localeData = json.values || json;

    if (!localeData) {
      console.warn('Localizer: Invalid data: %o for Locale: %o', localeData, localeName);
      throw new Error(`Localizer: Invalid data: ${localeData} for locale: ${localeName}`);
    }

    // Cache the key references for purging from the dictionary
    if (!localeFileInfo.keys) {
      localeFileInfo.keys = Object.keys(localeData);
    }

    // Fetches the locale from the cache
    // if data is present then merge the fetched data with the cached data
    // else cache the fetched data
    const cachedStrings = localesCache.get(localeName);
    let data = localeData;
    if (cachedStrings) {
      data = Object.assign(cachedStrings, localeData);
    }

    localesCache.set(localeName, data);
  }

  /**
   * @private
   * @function _deletePrevContextStrings
   * @description Delete previous context strings
   * @param {Object} prevContext - The locale file info { path:'', loaded: false }
   * @param {String} prevContext.id - Id of previous context
   */
  _deletePrevContextStrings(prevContext) {
    if (!prevContext
      || typeof prevContext !== 'object'
      || Array.isArray(prevContext)
      || Object.keys(prevContext).length < 1
      || !prevContext.id) {
      return;
    }

    const localesData = localesFileCache.get(prevContext.id);

    if (!localesData) {
      return;
    }

    for (const [localeId, { keys }] of Object.entries(localesData)) {
      if (!keys) { return; }

      const cachedStrings = localesCache.get(localeId);
      if (cachedStrings) {
        for (const key of keys) {
          delete cachedStrings[key];
        }
      }
    }
  }

  /**
   * @public
   * @function switchLocale
   * @description Switches the locale for the current displayed apps/panels,
   * this is different from the first time language loading methods above
   * @param {Object} data - Data to switch locale
   * @param {String} data.localeId - The locale to switch to en-gb, fr etc.
   * @param {Object} data.contexts - The array of active apps/references, to ensure only
   * the needed key files are loaded
   * @param {String} data.prevContext - Info about the previous context
   * @param {Function} callback - Optional callback function
   */
  async switchLocale(data, callback) {
    if (!data || typeof data !== 'object' || Array.isArray(data)) {
      console.warn('Localizer: Invalid data: %o provided to switch locale', data);
      return;
    }
    const { localeId, contexts, prevContext } = data;
    if (typeof localeId !== 'string' || localeId === ''
      || typeof contexts !== 'object' || Array.isArray(data)) {
      console.warn('Localizer: Invalid locale id: %o or contexts: %o, provided to switch locale', localeId, contexts);
      return;
    }

    if (prevContext) {
      this._deletePrevContextStrings(prevContext);
    }

    let { localeInfo } = data;
    if (!localeInfo) {
      localeInfo = {
        localeToLoad: localeId,
      };
    }

    const loaders = Object.values(contexts)
      .map(itemRef => itemRef && itemRef.id)
      .filter(Boolean);

    await Promise.all(loaders.map(async (contextId) => {
      await this.load({ localeInfo, contextId });
    }));

    _loc = localesCache.get(localeId) || {};
    if (typeof callback === 'function') { callback(); }
  }

  /**
   * @public
   * @function getParagraphs
   * @description Multiple paragraphs can be supplied in a single string with a /r
   * separator, use this function to split these into an array
   * @param {String} stringKey - The string to split into paragraphs, it is expected
   * that /r will be present in the strings which is the identifier
   * @returns {String} String value with parsed paragraphs
   */
  getParagraphs(stringKey) {
    const { loc } = this;
    if (!loc || typeof stringKey !== 'string' || stringKey === '') {
      console.warn('Localizer: Invalid string key: %o or loc object: %o provided to parse paragraphs', stringKey, loc);
      return null;
    }
    const stringValue = this.loc[stringKey];
    if (!stringValue) {
      console.warn('Localizer: Invalid string key: %o provided to parse paragraphs', stringValue);
      return [];
    }
    const parsedStringValues = stringValue.split('/r');
    return parsedStringValues;
  }

  /**
   * @public
   * @function replace
   * @description Replace instances of variables in a string
   * @param {String} stringKey - The string to replace variables for
   * @param {Array} variables - The array of variables to replace in the format
   * [{ name: 'my_variable', value: 'some value'}]
   */
  replace(stringKey, variables) {
    const { loc } = this;
    if (loc && !loc[stringKey]) {
      console.warn('Localizer: Invalid string:%o provided to replace', stringKey);
      return null;
    }
    let stringValue = loc[stringKey];
    if (variables) {
      variables.forEach((variable) => {
        const { name, value } = variable;
        const regExp = new RegExp(`{{${name}}}`, 'g');
        stringValue = stringValue.replace(regExp, value);
      });
    }
    return stringValue;
  }

  /**
  * @public
  * @function date
  * @description Switches the language for the current displayed apps/panels
  * this is different from the first time language loading methods above
  * @param {String} date - Date to localize
  * @param {Boolean} showTime - true/false, show/hide time
  * @param {String} loc - The language to switch to en-gb, fr etc.
  * @param {Object} opts - Options to set if time needs to be shown
  * @param {Function} callback - Optional callback function
  * TODO: Convert parameters to an object
  */
  date(date, showTime = false, opts = {}, loc = null) {
    // TODO: Experimental
    // console.log('localizer.date()', loc, date, showTime, opts);

    // ISO Date "2015-03-25" (The International Standard)
    // YYYY-MM-DDTHH:MM:SSZ
    // Short Date "03/25/2015"
    // MM/DD/YYYY OR DD/MM/YYYY
    // Long Date "Mar 25 2015" or "25 Mar 2015"
    // MMM DD YYYY

    // console.log('localizer.date().parse', loc, Date.parse(date));

    let returnStr = '';
    const dateLocale = loc || this.defaultDateLocale;
    // Replace any underscores to the standardized format using -
    const locale = dateLocale.replace('_', '-');
    // // Replace any dates spaces with T for IE support
    // const dateValue = date.replace(' ', 'T');
    // Default format the date time
    let format = new Date(date);
    // Separate the date and the time
    const dateTime = date.split('T');

    // console.log('localizer.date().format', format, dateTime);
    const day = format.getDate();
    const month = format.getMonth();
    const year = format.getFullYear();

    let time = '';
    let dateUTC = '';
    let offset = format.getTimezoneOffset();
    offset = -offset;

    // console.log('localizer.date().d.m.y', day, month, year);
    // console.log('localizer.date().dateValue', dateValue);

    // Conditional check to determine if a time was supplied.
    if (dateTime[1] !== undefined) {
      time = dateTime[1].split(':');
      const hours = time[0];
      const mins = time[1];
      const seconds = time[2];
      dateUTC = Date.UTC(year, month, day, hours, mins, seconds);
    } else {
      dateUTC = Date.UTC(year, month, day);
    }

    // Format the current date with the timezone offset
    format = new Date(dateUTC + (offset * 60 * 1000));

    // Determine if the time is meant to be returned
    if (showTime) {
      opts.timeZone = 'UTC';
      returnStr = format.toLocaleString(locale, opts);
    } else {
      returnStr = format.toLocaleDateString(locale, opts);
    }
    return returnStr;
  }

  /**
   * @public
   * @function purge
   * @description Purges the references to a specific context
   * @param {String} contextId - The id of context {'default, app-1'}
   */
  purge(contextId) {
    const isCleared = this._deleteLocaleDataFromCache(contextId);
    if (isCleared) {
      localesFileCache.delete(contextId);
    }
  }

  /**
   * @private
   * @function _deleteLocaleDataFromCache
   * @description Deletes locale data from cache for provided context Id
   * @param {String} contextId - The id of context {'default, app-1'}
   */
  _deleteLocaleDataFromCache(contextId) {
    if (typeof contextId !== 'string' || contextId === '') {
      console.warn('Localizer: Can\'t delete locale data of item: %o', contextId);
      return false;
    }

    const localesFilesData = localesFileCache.get(contextId);
    if (!localesFilesData || typeof localesFilesData !== 'object' || Array.isArray(localesFilesData)) {
      console.warn('Localizer: Can\'t remove the references from localesFilesData: %o', localesFilesData);
      return false;
    }

    const isDeleted = Object.keys(localesFilesData).every((localeName) => {
      try {
        const StringKeys = localesFilesData[localeName].keys;
        const localeCache = localesCache.get(localeName);
        const isCleared = StringKeys.every((StringKey) => {
          if (!localeCache[StringKey]) {
            return false;
          }

          delete localeCache[StringKey];
          return true;
        });

        // Don't clear the cache if all the strings are not deleted
        // Added to check if a string exists in the context(eg: app-1) locale file
        // but its not stored in the localesCache
        if (!isCleared) { return false; }

        if (Object.keys(localeCache).length < 1) {
          localesCache.delete(localeName);
        }
        return true;
      } catch (e) {
        console.warn('Localizer: Unable to delete Cache: %o', e);
        return false;
      }
    });
    return isDeleted;
  }

  /**
   * @public
   * @function clear
   * @description Clears the locale cache, locale file cache
   * and the currently loaded locale strings(loc)
   */
  clear() {
    localesFileCache.clear();
    localesCache.clear();
    _loc = {};
  }
}

const obj = new Localizer();
export default obj;
