/**
 * @class Router
 * @description A simple router that is capable of routing hash and history api calls
 * parsing query parameters and REST uri with embedded variables, the supported path types
 * are below:
 * HISTORY
 * localhost/mypath
 * localhost/mypath/mysubpath
 * localhost/mypath/mysubpath?x=1
 * localhost/mypath#mysubpath?x=1

 * HASH
 * localhost/#mypath
 * localhost/#mypath/mysubpath
 * localhost/#mypath/mysubpath?x=1
 *
 * AND The capacity to auto parse rest variables and separate from the app appPaths
 * localhost:8080/#app-1/app-2/app-3/!var1/1/!var2/2?qVar1=22&qVar2=33
 */
class Router {
  constructor() {
    let _activeAppId = null;
    this.setActiveAppId = (appId) => {
      _activeAppId = appId;
    };
    this.getActiveAppId = () => _activeAppId;
  }

  /**
   * @public
   * @function setOptions
   * @description The setOptions function for the router
   * @param {Object} options - The options object for the router
   */
  setOptions(options) {
    const defaultOptions = {
      mode: 'hash',
      appRoot: '/',
      autoUrlDecode: false,
      // The app will attempt to map key value pairs if this options is set to true
      // NOTE - But how does one know when the app paths end and the variables begin
      // i.e. /app-1/app-2/app-3/var1/1/var2/2
      // Perhaps add a var prefix...
      // i.e. /app-1/app-2/app-3/!var1/1/!var2/2
      autoRestVars: false,
      varPrefix: '!',
      timeout: 10,
      handlers: {},
      routes: {},
      // Set the current uri reference
      currentHref: '',
      currentPath: '/',
    };

    const _options = { ...defaultOptions, ...options };
    Object.entries(_options).forEach(([key, value]) => {
      this[key] = value;
    });

    this._bindMethods();
  }

   /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this._checkUrl = this._checkUrl.bind(this);
    this.listen = this.listen.bind(this);
    this._handleLocationChange = this._handleLocationChange.bind(this);
    this.addRoute = this.addRoute.bind(this);
    this.goto = this.goto.bind(this);
  }

  /**
   * @private
   * @function _guidGenerator
   * @description Generate a unique id for the listener callbacks
   */
  _guidGenerator() {
    const S4 = () => {
      const rand = Math.floor((1 + Math.random()) * 0x10000);
      return rand.toString(16).substring(1);
    };
    const time = new Date().getTime();
    return `${S4()}${S4()}-${S4()}-${S4()}-${S4()}-${S4()}${S4()}${S4()}-${time}`;
  }

  /**
   * @public
   * @function goto
   * @description Register a new route and callback for the specific path
   * @param {String} path - The path to register the route for
   */
  goto(path) {
    let cleanPath = null;
    if (this.mode === 'hash') {
      const location = this._getWindow('location');
      cleanPath = location.href.replace(/#(.*)$/, '');
      location.href = `${cleanPath}#${path}`;
    } else if (this.mode === 'history') {
      cleanPath = this._cleanURL(path);
      const history = this._getWindow('history');
      history.pushState(null, null, `${this.appRoot}${cleanPath}`);
    }
  }

  /**
   * @function _getWindow
   * @description returns property on the global window object
   * @param {String} - Property name
   */
  _getWindow(prop) {
    return window[prop];
  }

 /**
  * @private
  * @function _checkUrl
  * @description Check the currentURL for location changes based on the current href
  */
  _checkUrl() {
    const location = this._getWindow('location');
    if (location.href !== this.currentHref) {
      this._handleLocationChange();
    }
  }

  /**
   * @public
   * @function listen
   * @description Initialize the listening for location changes
   * @param {Function} callback - The optional callback starter function for the listener
   * @returns {String} id of the listener
   */
  listen(callback) {
    const id = this._addListener(callback);

    const {
      mode,
      _handleLocationChange,
      _checkUrl,
      timeout,
      timer,
    } = this;

    // If the mode is hash, use the standard event listener
    if (mode === 'hash') {
      // Execute the handle location change event,
      // this is the default action when listening occurs
      const addEventListener = this._getWindow('addEventListener');
      addEventListener('hashchange', _handleLocationChange, false);
    } else if (mode === 'history') {
      if (timer) {
        const clearInterval = this._getWindow('clearInterval');
        clearInterval(timer);
      }
      // There is no event change for the address url and thus we need to listen
      const setInterval = this._getWindow('setInterval');
      this.timer = setInterval(_checkUrl, timeout);
    }

    _handleLocationChange();

    // Return the id of the default listener
    return id;
  }

  /**
   * @private
   * @function _addListener
   * @description Add a new location change event listener
   * @param {Function} callback - The callback function to store as a handler
   */
  _addListener(callback) {
    if (typeof callback !== 'function') {
      return null;
    }
    const id = this._guidGenerator();
    this.handlers[id] = callback;
    return id;
  }

  /**
   * @public
   * @function removeListener
   * @description Remove a listener from the object
   * @param {String} id - The id of the listener to remove
   */
  removeListener(id) {
    delete this.handlers[id];
  }

  /**
   * @public
   * @function addRoute
   * @description Register a new route and callback for the specific path, this is different
   * to adding a listener because it will only fire the for the specific route
   * @param {String} path - The path to register the route for
   * @param {Function} callback - Callback function for the path
   */
  addRoute(path, callback) {
    const warningMsg = 'Invalid path provided';
    let id = null;

    if (!path) {
      console.warn(warningMsg);
      return id;
    }

    const pathType = typeof path;
    if (pathType === 'string' || pathType === 'object' || !Array.isArray(pathType)) {
      const obj = { path, callback };
      id = this._guidGenerator();
      this.routes[id] = obj;
    } else {
      console.warn(`${warningMsg}: %o`, path);
    }

    return id;
  }

  /**
   * @public
   * @function removeRoute
   * @description Remove a specific route listener by path or ID
   * @param {String} param - The value can either be the GUID or the Path, note that if the path
   * is used then ALL listeners on that specific path will be deleted
   */
  removeRoute(param) {
    // if param is guid
    if (this.routes[param]) {
      delete this.routes[param];
      return;
    }

    // expects the param to be a path
    Object.entries(this.routes).forEach(([key, value]) => {
      if (value.path === param) {
        delete this.routes[key];
      }
    });
  }

  /**
   * @private
   * @function _checkRoutes
   * @description Check the stored routes to determine if any match the current route
   * and execute the respective callback
   * @param {String} path - The path string to check for
   * @param {Object} options - The options containing any rest variables
   * @param {Object} query - Any query values passed in the url
   */
  _checkRoutes(path, options, query) {
    const { routes } = this;
    if (!routes || typeof routes !== 'object' || Array.isArray(routes)) { return; }

    const routeIds = Object.keys(routes);
    if (routeIds.length < 1) { return; }

    if (!path || typeof path !== 'string' || path === '') {
      console.warn('Router: Unable to check routes for the given path: %o', path);
      return;
    }

    routeIds.forEach((routeId) => {
      const { path: routePath, callback } = routes[routeId];
      const routePathType = typeof routePath;

      let match = false;
      const isPathString = (routePathType === 'string' && routePath === path);
      match = isPathString || (routePathType === 'object' && routePath.test(path));

      if (match && typeof callback === 'function') {
        callback(path, options, query);
      }
    });
  }

  /**
   * @private
   * @function _handleLocationChange
   * @description Handles the locations change event, gets the current path details
   * and dispatches to the referenced callbacks
   */
  _handleLocationChange() {
    const {
      path,
      subpaths,
      options,
      query,
    } = this._getLocation();

    // Loop through the location change handlers stored by the router
    // and execute each function passing the path and the routing options
    Object.values(this.handlers).forEach(callback => callback({
      path, subpaths, options, query,
    }));
  }

  /**
   * @private
   * @function _getLocation
   * @description Gets the current location
   * @returns {Object} parsed location into { path, subpaths, options, query }
   */
  _getLocation() {
    const location = this._getWindow('location');
    this.currentHref = location.href;

    const { query, hash, url } = this._parseCurrentLocation();
    const _data = {
      path: '/',
      subpaths: null,
      options: {},
      query,
    };

    let data = null;
    const { mode } = this;
    if (mode === 'hash') {
      data = this._hashMode(hash, query);
    } else if (mode === 'history') {
      data = this._historyMode(url, query);
    }

    if (!data) {
      return _data;
    }

    const {
      root: path,
      paths: subpaths,
      params: options,
    } = data;

    Object.assign(_data, { path, subpaths, options });

    return _data;
  }

  /**
   * @private
   * @function _parseCurrentLocation
   * @description Parses the current location URL
   * @returns {Object} query, hash, url
   */
  _parseCurrentLocation() {
    const { currentHref, autoUrlDecode } = this;

    let url = currentHref;
    if (autoUrlDecode) {
      url = decodeURIComponent(url);
    }

    let hashURL = null;
    [url, hashURL] = url.split('#');

    let query = null;
    let app = null;
    if (hashURL) {
      [app, query] = hashURL.split('?');
    }
    query = this._parseQuery(query);

    url = this._cleanURL(url);

    return { query, hash: app, url };
  }

  /**
   * @private
   * @function _hashMode
   * @description Executes the required hash mode changes
   * @param {String} hash - Hash path
   * @param {Object} query - Parsed query
   * @returns {Object} { params, paths, root }
   */
  _hashMode(hash, query) {
    hash = this._cleanURL(hash);

    // If we are in hash mode the then the hash is the root path fragment
    if (!hash || hash === '') {
      // commenting this code as it causes the default package to be loaded twice
      // this triggers the hashchange event and calls _handleLocationChange()
      // which triggers Context._handleLocationChanged() and loads the default package again.
      // window.location.hash = '#';
      return null;
    }

    // What if there is an extended fragment on the hash i.e. #maypath/myotherpath
    const parsedURL = this._parseURL(hash);

    this._checkRoutes(hash, parsedURL.params, query);
    return parsedURL;
  }

  /**
   * @private
   * @function _historyMode
   * @description Executes the required history mode changes
   * @param {String} url - Current location URL
   * @param {Object} query - Parsed query
   * @returns {Object} { params, paths, root }
   */
  _historyMode(url, query) {
    const location = this._getWindow('location');
    const [, fullPath] = url.split(`${location.origin}${this.appRoot}`);
    const parsedURL = this._parseURL(fullPath);

    this._checkRoutes(fullPath, parsedURL.params, query);
    return parsedURL;
  }

  /**
   * @private
   * @function _cleanURL
   * @description Cleans a url and removes any slashes from the beginning and end
   * @param {String} url - The url to clean
   * @returns {String} Cleaned URL
   */
  _cleanURL(url) {
    if (url) {
      return url.toString().replace(/\/$/, '').replace(/^\//, '');
    }
    return url;
  }

  /**
   * @private
   * @function _parseURL
   * @description Parses the URL rest parameters
   * @param {String} url - The url to parse
   * @returns {Object}  Parsed URL into { root, paths, params }
   */
  _parseURL(url) {
    const parsedURL = {
      root: '/',
      paths: null,
      params: null,
    };

    if (!url) {
      return parsedURL;
    }

    if (!this.autoRestVars) {
      const [root, ...paths] = url.split('/');
      Object.assign(parsedURL, { root, paths });
      return parsedURL;
    }

    const [path, ...params] = url.split(this.varPrefix);
    const restParams = params.reduce((acc, cv) => {
      const [key, value] = cv.split('/');
      acc[key] = value;
      return acc;
    }, {});

    let _path = path;
    if (_path.endsWith('/')) {
      _path = _path.slice(0, _path.length - 1);
    }
    const [root, ...appPaths] = _path.split('/');

    return { root, paths: appPaths, params: restParams };
  }

  /**
   * @private
   * @function _parseQuery
   * @description Parse query options into a javascript object
   * @param {String} query - The query string to parse into key/values
   * @returns {Object} Parsed query
   */
  _parseQuery(query) {
    if (!query) {
      return {};
    }

    const keyValues = query.split('&');
    const parsedQuery = keyValues.reduce((acc, cv) => {
      const [key, value] = cv.split('=');
      acc[key] = value || '';
      return acc;
    }, {});
    return parsedQuery;
  }
}

const obj = new Router();
export default obj;
