/* global SystemJS */
import Load from './PromiseLoader';
import Localizer from './Localizer';

class PackageLoader {
  /**
   * @public
   * @function load
   * @description The system package loader, which currently leverages system JSON
   * This has the optional capacity to load an App config if it was not supplied
   * by the App parent
   * @param {Object} data - Data for loading a package
   * @param {String} data.path - Path to the package
   * @param {String} data.packageName - Package name to load
   * @param {Object} data.config - Package config
   * @param {Object} data.localeInfo - locale info to load
   * @param {Object} data.prevContext - info about the previous context
   * @returns {Object} Promise with {module, config}
   */
  async load(data) {
    if (!data || typeof data !== 'object' || Array.isArray(data)) {
      console.warn('PackageLoader: Invalid data: %o provided to load package', data);
      return null;
    }
    const {
      packageName, config, localeInfo, prevContext,
    } = data;
    const path = data.path || '';

    const _packageConfig = await this._mergeConfigs({
      path,
      packageName,
      config,
      localeInfo,
    });

    await this._loadPackageLocale({
      path,
      packageName,
      packageConfig: _packageConfig,
      localeInfo,
      prevContext,
    });

    const { map, shareableComponents } = this._mapPackageDependencies(_packageConfig);

    try {
      await this._loadShareableComponents(shareableComponents);
    } catch (e) {
      console.warn('PackageLoader: Unable to load shareable components, error: %o', e);
    }

    SystemJS.config({ map });

    const pckg = await SystemJS.import(
      `${`${path}${_packageConfig.script}` || `${path}${packageName}/Main`}.js${
        window.EUI_TS ? `?${window.EUI_TS}` : ''
      }`,
    );
    return new Promise((resolve) => {
      resolve({ module: pckg, config: _packageConfig });
    });
  }

  /**
   * @private
   * @description returns the property attached to the window object
   * @param {String} prop - property to fetch
   * @returns {Object/function/String} property attached to global window object
   */
  _getWindow(prop) {
    return window[prop];
  }

  /**
   * @private
   * @description Load shareable components
   * @function _loadShareableComponents
   * @param {Array} shareableComponents - shareableComponents to load
   */
  async _loadShareableComponents(shareableComponents) {
    await Promise.all(
      shareableComponents.map(this._loadShareableComponent.bind(this)),
    );
  }

  /**
   * @private
   * @description Load shareable component
   * @function _loadShareableComponent
   * @param {Object} shareableComponent - shareableComponent to load
   */
  async _loadShareableComponent(shareableComponent) {
    const _fetch = this._getWindow('fetch');
    const compConfig = await _fetch(
      `${shareableComponent}/config.json${window.EUI_TS ? `?${window.EUI_TS}` : ''}`,
    );
    const compConfigJSON = await compConfig.json();
    const { map } = this._mapPackageDependencies(compConfigJSON);
    SystemJS.config({ map });

    const pckg = await SystemJS.import(`${shareableComponent}/Main.js${window.EUI_TS ? `?${window.EUI_TS}` : ''}`);

    return new Promise((resolve) => {
      resolve({ module: pckg, config: compConfigJSON });
    });
  }

  /**
   * @function _mergeConfigs
   * @param {String} Object.path - Path to the package
   * @param {String} Object.packageName - Package name to load
   * @param {Object} Object.packageConfig - Package configuration
   * @returns {Object} Merged package config
   */
  async _mergeConfigs({ path, packageName, packageConfig }) {
    // always load the locale config and merge it with parent config
    const localConfig = await Load.json(`${path}${packageName}/config.json`);
    const _packageConfig = { ...localConfig, ...packageConfig };
    return _packageConfig;
  }

  /**
   * @private
   * @function _loadPackageLocale
   * @description Loads current locale for the package
   * @param {String} Object.path - Path to the package
   * @param {String} Object.packageName - Package name to load
   * @param {Object} Object.packageConfig - Package configuration
   * @param {Object} Object.localeInfo - Information about locale to load
   * @param {Object} Object.prevContext - Information about previous context
   */
  async _loadPackageLocale({
    path, packageName, packageConfig, localeInfo, prevContext,
  }) {
    // Check for the locale Path and configure the localizer to load
    const { localePath } = packageConfig;
    if (localePath) {
      localeInfo.localesPath = `${path}${packageName}/${localePath}`;
      await Localizer.load({
        contextId: packageName,
        localeInfo,
        prevContext,
      });
    } else {
      await Localizer.load({
        contextId: packageName,
        prevContext,
      });
    }
  }

  /**
   * @private
   * @function _mapPackageDependencies
   * @description Maps package dependencies
   * @param {Object} packageConfig - Package configuration data
   * @returns {Object} - Mapped dependency paths and shareable components to load
   */
  _mapPackageDependencies(packageConfig) {
    const { containerConfig } = window;
    if (!containerConfig) {
      console.warn('PackageLoader: default container config not found');
    }
    const { libsPath: _libsPath = '' } = this._getWindow('containerConfig') || {};

    const { dependencies, components } = packageConfig;

    if (!!dependencies && (typeof dependencies !== 'object' || Array.isArray(dependencies))) {
      console.warn('PackageLoader: Invalid type of dependencies');
      return null;
    }

    if (!!components && (typeof components !== 'object' || Array.isArray(components))) {
      console.warn('PackageLoader: Invalid type of components');
      return null;
    }

    const dependencyKeys = Object.keys(dependencies || {});
    const componentsKeys = Object.keys(components || {});

    if (dependencyKeys.length < 1 && componentsKeys.length < 1) {
      return null;
    }

    const configKeysPath = {
      $libs: 'libsPath',
      $panels: 'panel/panelsPath',
      $apps: 'app/appsPath',
      $components: 'component/componentsPath',
    };

    let configKeys = null;
    const callback = (data) => {
      configKeys = data;
      // will be undefined as it is attached to window, TODO fix
      configKeys.$libs = _libsPath;
    };

    const _event = new CustomEvent('GET_CONFIG', {
      detail: {
        configKeysPath,
        callback,
      },
    });

    document.dispatchEvent(_event);

    const map = {};
    dependencyKeys.forEach((key) => {
      const majorVersion = dependencies[key].match(/\d+/g)[0];
      const _key = `${configKeys.$libs}${key}/${majorVersion}/Main.js${
        window.EUI_TS ? `?${window.EUI_TS}` : ''
      }`;
      map[key] = _key;
    });

    const shareableComponents = [];
    componentsKeys.forEach((key) => {
      const majorVersion = components[key].match(/\d+/g)[0];
      const compHome = `${configKeys.$components}${key}/${majorVersion}`;
      const _key = `${compHome}/Main.js${window.EUI_TS ? `?${window.EUI_TS}` : ''}`;
      shareableComponents.push(compHome);
      map[key] = _key;
    });

    return { map, shareableComponents };
  }

  /**
   * @public
   * @function loadConfig
   * @description Loads container config
   * @returns {Object} Container config
   */
  async loadConfig() {
    // Load the base path file, but do not use system JS as it has not been configured yet
    // Base is expected and should always be there in the folder relative to the container
    // const base = await Load.json('./path.json');
    // Set a default config path in the event one is not found in the base file
    const { config: configPath = 'config.json' } = this._getWindow('containerConfig') || {};
    return Load.json(configPath);
  }
}

const obj = new PackageLoader();
export default obj;
