/**
 * @function addSharedCSS
 * @description Add the shared css to the supplied markup
 * @param {String} markup - The markup to prepend the CSS path to
 */
export const addSharedCSS = (markup) => {
  if (typeof markup !== 'string') {
    console.warn('general: Invalid markup: %o provided', markup);
    return null;
  }

  const { sharedCSS: path = 'assets/css/container.shared.css' } = window.containerConfig || {};
  const css = `<link rel="stylesheet" href="${path}${window.EUI_TS ? `?${window.EUI_TS}` : ''}">`;
  return css + markup;
};

/**
 * @function findElements
 * @description Find the supplied elements in the dom of the supplied scope
 * @param {Object} scope - The scope of the dom to search through
 * @param {Array/Object} selectors - The array of element selectors to find or
 * alternatively and object with key value pairs
 */
export const findElements = (scope, selectors) => {
  const obj = (scope.root !== undefined) ? scope.root : scope;
  if (Array.isArray(selectors)) {
    selectors.forEach(({ name, selector }) => {
      scope[name] = obj.querySelector(selector);
    });
    return;
  }
  Object.entries(selectors).forEach(([name, selector]) => {
    scope[name] = obj.querySelector(selector);
  });
};
