import '@eui/theme';
import Cache from './Cache';
import Config from './Config';
import BrowserUtils from '../utils/Browser';
import Functions from '../utils/Functions';
import Container from '../layout-components/container/Container';
import {
  AppStore,
  Load,
  PackageLoader,
  Router,
  Plugins,
  Localizer,
} from '../util-helpers';
import {
  SWITCH_THEME,
  LOCALE_UPDATED,
  UPDATE_LOCALE,
  SET_LOCALE,
} from '../utils/Actions';

/**
 * @class Context
 * @description  The main application context which is the main entry container initialization
 */
export default class Context {
  /**
   * @function constructor
   * @description Constructor for the context
   */
  constructor() {
    window.EUI = { Localizer, Router };
  }

/**
 * @function _getWindow
 * @description returns property on the global window object
 * @param {String} prop - Property name
 */
  _getWindow(prop) {
    if (!prop) {
      return window;
    }
    return window[prop];
  }

  /**
   * @function preload
   * @description Preload any JavaScript configuration files and plugins
   */
  async preload() {
    const config = await this._loadContainerConfig();
    if (!config) { return; }

    this._setDefaultRoute();
    await this._loadPlugins();
    await this._loadMenuConfig();
    await this._init();
  }

  /**
   * @private
   * @function _loadContainerConfig
   * @description Loads the container config
   */
  async _loadContainerConfig() {
    const containerConfig = await PackageLoader.loadConfig();
    if (!containerConfig || typeof containerConfig !== 'object' || Array.isArray(containerConfig)) {
      console.warn('Context: Invalid container config: %o', containerConfig);
      return null;
    }

    const _config = this._mergeConfig(containerConfig);
    document.addEventListener('GET_CONFIG', this._configListener.bind(this));
    return _config;
  }

  /**
   * @private
   * @function _configListener
   * @description Listens to container config requests
   * @param {Object} e - custom event with config keys path and callback to be triggered
   */
  _configListener(e) {
    const { detail } = e;
    if (!detail) {
      console.warn('Context: Invalid detail provided');
      return;
    }

    const { configKeysPath, callback } = detail;
    if (!configKeysPath || !callback) {
      console.warn('Invalid keys path or callback provided');
      return;
    }

    const data = {};
    Object.entries(configKeysPath).forEach(([key, value]) => {
      data[key] = this._getConfig(value);
    });

    callback(data);
  }

  /**
   * @private
   * @function _getConfig
   * @description Gets the value of the config key
   * @param {String} path - config key path
   */
  _getConfig(path) {
    const pathSegments = path.split('/');
    let endVal = Config;
    try {
      pathSegments.forEach((val) => {
        endVal = endVal[val];
      });
    } catch (e) {
      endVal = null;
      console.warn('Context: Unable to find value for the provided key path: %s, e:%o', path, e);
    }
    return endVal;
  }

  /**
   * @private
   * @function _loadPlugins
   * @description Loads the plugins
   */
  async _loadPlugins() {
    const { plugin } = Config;
    if (!plugin || typeof plugin !== 'object' || Array.isArray(plugin)) {
      console.warn('Context: Invalid plugin: %o config provided', plugin);
      return;
    }
    const { pluginsPath, plugins, pluginTimeout } = plugin;
    Plugins.setPluginTimeout(pluginTimeout);
    await Plugins.load(pluginsPath, plugins);
  }

  /**
   * @private
   * @function _loadMenuConfig
   * @description Loads the apps config
   */
  async _loadMenuConfig() {
    const { menu: { menuConfig } = {} } = Config;
    if (typeof menuConfig !== 'string' || menuConfig === '') {
      console.warn('Context: Invalid menu config path: %o provided', menuConfig);
      return;
    }

    const _menuConfig = await Load.json(`${menuConfig}`).catch((e) => {
      console.warn('Context: Unable to load menu config file: %o stack: %o', menuConfig, e);
      this._init();
    });

    // Set the App Config reference in the namespace to avoid decorating object
    window.EUI.MenuConfig = _menuConfig;
  }

  /**
   * @private
   * @function _loadLocale
   * @description Loads locale
   */
  async _loadLocale() {
    const { i18n } = Config;
    if (!i18n || typeof i18n !== 'object' || Array.isArray(i18n)) {
      console.warn('Context: Invalid i18n: %o config provided', i18n);
      return;
    }
    const {
      defaultLocale,
      defaultDateLocale,
      localeIds,
      localesType,
      localesPath,
    } = i18n;

    // Set options for Localizer
    Localizer.setOptions({
      localesType,
      defaultDateLocale: defaultDateLocale || 'en-us',
      defaultLocale,
      localesPath,
      localeIds,
    });

    let localeToLoad;
    if (this.overrideLocaleOnStart) {
      localeToLoad = this.overrideLocaleOnStart;
    } else {
      localeToLoad = BrowserUtils.checkBrowserLocale(Config.i18n) || defaultLocale || 'en-us';
    }

    await Localizer.load({
      contextId: 'default',
      localeInfo: {
        localeToLoad,
        localeIds,
        localesPath,
      },
    }, () => {
      AppStore.dispatch(LOCALE_UPDATED, { locale: localeToLoad });
    });

    // Set up a listener for the update locale and apply the necessary
    // functions after the languages have been set
    AppStore.listen(UPDATE_LOCALE, this._executeLocaleSwitch);
  }

  /**
   * @private
   * @function _findMappedRoute
   * @description Find mapped route
   */
  _findMappedRoute(routeMap, routeToFind) {
    return routeMap[routeToFind] || routeToFind;
  }

  /**
   * @private
   * @function _setDefaultRoute
   * @description Attaches container's default route to EUI namespace
   */
  _setDefaultRoute() {
    const { routeMap } = Config.router;
    const { defaultApp } = Config.app;
    window.EUI.defaultRoute = this._findMappedRoute(routeMap, defaultApp);
  }

  /**
   * @private
   * @function _init
   * @description Initialize the container, theme, locale, etc...
   * Execute all plugins that implement onBeforeContainerLoad.
   * Give the opportunity for a plugin to override/set the locale and/or theme.
   */
  async _init() {
    this._configure();
    this._initTheme();

    // set locale action handler
    const setLocale = (localeToLoad) => {
      this.overrideLocaleOnStart = localeToLoad || 'en-us';
      AppStore.setState('locale', this.overrideLocaleOnStart);
    };

    AppStore.listen(SET_LOCALE, setLocale);

    await Plugins.executeAll('onBeforeContainerLoad', { store: AppStore });
    await this._loadLocale();
    this._initContainer();
    this._start();

    // un-listen to the "SET_LOCALE" action as this is only valid during startup of the Container.
    AppStore.unlisten(SET_LOCALE, setLocale);
  }

  /**
   * @private
   * @function _mergeConfig
   * @description Merges the container config with the internal config
   * @param {Object} containerConfig - Container config
   */
  _mergeConfig(containerConfig) {
    containerConfig.private = Config.private;
    this._initI18n(containerConfig);
    // Updating the global config for other components to import
    Object.assign(Config, containerConfig);
    return containerConfig;
  }

  /**
   * @private
   * @function _initI18n
   * @description If i18n config is not provided by the container client
   * then move the i18n config from internal private config
   * @param {Object} containerConfig - Merged config
   * @returns {Object} config - Updated config with i18n settings
   */
  _initI18n(containerConfig) {
    if (containerConfig.i18n) {
      return containerConfig;
    }
    containerConfig.i18n = Config.private.i18n;
    return containerConfig;
  }

  /**
   * @private
   * @function _configure
   * @description Configures the container
   */
  _configure() {
    const privateConfig = Config.private;
    const values = {
      isLoggedIn: false,
      locale: BrowserUtils.checkBrowserLocale(Config.i18n) || Config.i18n.defaultLocale || 'en-us',
      activeAppPath: null,
      ...privateConfig,
    };
    AppStore.initialState(values);
    Router.setOptions(Config.router);
  }

  /**
  * @private
  * @function _initTheme
  * @description Initialize the theme component
  */
  _initTheme() {
    const theme = document.createElement('eui-theme-v0');
    theme.fontPath = window.containerConfig.fontPath;
    document.body.appendChild(theme);
    Cache.storeInternal('theme', theme);

    // Set up the theme switch listener which will set the prop in the theme
    // component in the previous version of the container, several components
    // had theme props. This was only needed for the V1 theme.
    AppStore.listen(SWITCH_THEME, this._switchTheme);
  }

  /**
   * @private
   * @function _initContainer
   * @description Initialize the container
   */
  _initContainer() {
    // Register the Container component
    Container.register();
    const container = new Container();
    document.body.appendChild(container);
    Cache.storeInternal('container', container);
  }

  /**
   * @private
   * @function _start
   * @param {Object} config - Container config
   * @description Starts the application
   */
  _start() {
    // Before continuing check the authorization, this method is bound to the
    // authorization plugin.
    this._checkAuthorization();

    // Initialize the cache Timer
    this._cacheTimer();

    // Initialize the router listener, this occurs after the initial component
    // render to ensure there are components to route to
    Router.listen(this._handleLocationChanged);
    // person.hello.apply(person, arguments);
  }

  /**
   * @private
   * @function _checkAuthorization
   * @description Check the authorization for the user to access the container
   * this is done using the authorization plugin which would be customized to
   * to suit the way in which the project authorizes a user
   */
  _checkAuthorization() {
    const { plugin, validate, redirect } = Config.authentication;
    if (validate) {
      const { name, method } = plugin;
      // Check for the authentication plugin and check the status of the user
      const status = Plugins.find(name, method);
      AppStore.setState('isLoggedIn', status);
      if (!status) {
        this._getWindow().location = redirect;
        return false;
      }
    }
    return true;
  }

  /**
  * @private
  * @function _cacheTimer
  * @description Determines whether the cache autoClear has been set and initializes
  * the interval check for the clearing
  */
  _cacheTimer() {
    const { cache: cacheConfig } = Config;
    if (!cacheConfig) { return; }

    const { autoClear, timeout } = cacheConfig;
    if (autoClear) {
      const _timeout = (timeout || 20) * 1000 * 60;
      this.cacheTimer = setInterval(() => {
        // Execute the check cache on the layout LayoutHolder
        Cache.checkCache(cacheConfig.maxAge);
      }, _timeout);
    }
  }

  /**
   * @private
   * @function _executeLocaleSwitch
   * @description Executes the request to switch the locale using the Localizer
   * @param {Object} data - locale date
   * @param {String} data.localeId - The locale to switch to en-us, en-gb
   * @param {Function} data.callback - The callback function to execute when the new language
   * @param {Function} data.context - Information about the context to load like app/panel
   * @param {Function} data.prevContext - Information about previous context like app/panel
   * @param {Function} data.localeInfo - Info for the locale to load strings have been cached
   */
  _executeLocaleSwitch(data) {
    const {
      localeId, callback, context, prevContext, localeInfo,
    } = data;
    // NOTE - RTL Switching was a hack and has not been added in.
    // Consider how this might be better placed, need more
    // in-depth locale reference for rtl languages and input from EDS
    // this.checkUIAlignment(locale);

    // Get the current loaded apps, so that the appropriate files
    // can be loaded if they have not already be loaded
    let contexts = context;
    if (!context) {
      contexts = {
        default: { id: 'default' },
        ...Cache.getCurrentLoaded(),
      };
    }

    const dataToSwitchLocale = {
      localeId, contexts, prevContext, localeInfo,
    };
    // Execute the switch language method on the locale cache
    Localizer.switchLocale(dataToSwitchLocale, () => {
      if (!context) {
        // Set the Application state after the keys have been cached
        // in the dictionary
        AppStore.setState('locale', localeId);
        // Dispatch an action to components watching the store
        AppStore.dispatch(LOCALE_UPDATED, { locale: localeId });
        return;
      }

      if (typeof callback === 'function') { callback(); }
    });
  }

  /**
   * @private
   * @function _switchTheme
   * @description Switch to a valid theme
   * @param {String} theme - Theme name
   * @returns {Boolean} True, if the theme is switched else false
   */
  _switchTheme(theme) {
    let isThemeSwitched = false;
    if (typeof theme === 'string') {
      const themeComp = Cache.getInternal('theme');
      themeComp.theme = theme;
      AppStore.setState('theme', theme);
      isThemeSwitched = true;
    } else {
      console.warn('Context: Invalid theme: %o provided to switch', theme);
    }
    return isThemeSwitched;
  }

  /**
   * @private
   * @function _handleLocationChanged
   * @description The callback for location changed events
   * @param {Object} config - Container config
   * @param {Object} data - data for handling location changes
   * @param {String} data.path - The path fragment for the location change event
   * @param {Array} data.subpaths - The sub paths for the fragment
   * @param {Object} data.options - The options passed as part of the change event
   * @param {Object} data.query - The query passed as part of the change event
   */
  _handleLocationChanged = (data) => {
    if (!data || typeof data !== 'object' || Array.isArray(data)) {
      console.warn('Context: Invalid data: %o provided to handle changed location', data);
      return;
    }

    // NOTE - LOAD SEQUENCE
    // So we want to optimize the loading sequence for deeply nested apps, and
    // use the root app as the source for app nesting configs
    // thus if subpaths length > 0 then the config file or the entire APP
    // for the root path should be loaded and cached... This will also help
    // with routing to other applications path within the same parent group
    const {
      router: { routeMap, allowDirect, singleMode },
      app: { defaultApp, appsPath },
      i18n: { localeIds },
    } = Config;

    // Check if direct app path mapping should be disabled, i.e. not a valid route
    // in the list of available routes
    if (!allowDirect && !Object.values(routeMap).includes(data.path)) {
      console.warn('Context: Forbidden: You are not permitted to route to this path, check your config settings.');
      return;
    }

    const {
      path, subpaths, options, query,
    } = data;

    // Set the default path reference and a sub path variable
    let mappedPath = path;

    // Determine if the path is a "route" domain path, i.e. none specified
    // such as http://mydomain.com, in this event we load the default App
    if (mappedPath === '/' || mappedPath === '' || singleMode === true) {
      mappedPath = defaultApp;
    }

    // check if mapped route exists and substitute the path
    mappedPath = this._findMappedRoute(routeMap, mappedPath);

    // show the default app in URL if nothing specified
    if (path === '/') {
      Router.goto(mappedPath);
      return;
    }

    Router.setActiveAppId(mappedPath);

    const localeInfo = {
      localeToLoad: AppStore.getState('locale'),
      localeIds,
    };

    const locationParams = { options, query };
    const type = 'app';

    // Check to skip the main app loading if the container only supports 1 APP
    // In this event the container will not try to load sub routes as apps
    // but only load the default app if not already loaded, the default
    // app can then listen for route changes
    if (singleMode) {
      const cached = Cache.isPackageCached(mappedPath);
      if (!cached) {
        Functions.loadApp({
          packageName: mappedPath,
          type,
          localeInfo,
          locationParams,
          path: appsPath,
        });
      }
      return;
    }

    // Determine if there are any sub paths to load which represent child applications
    // which require their parents to load, as they hold the full configuration
    // i.e. app-1/app-3 where app-3 is the child and app-1 is the parent
    // The parent config is required as it contains the "breadcrumb details"
    if (subpaths && subpaths.length > 0) {
      let mappedSubPath = subpaths[subpaths.length - 1];

      // Check to determine if there is a mapped subpath and substitute the path
      mappedSubPath = this._findMappedRoute(routeMap, mappedSubPath);

      Router.setActiveAppId(mappedSubPath);

      // Now load the child app
      Functions.loadApp({
        packageName: mappedSubPath,
        type,
        localeInfo,
        locationParams,
        path: appsPath,
      });
      return;
    }

    // There are no sub apps, thus simple root app load
    Functions.loadApp({
      packageName: mappedPath,
      type,
      localeInfo,
      locationParams,
      path: appsPath,
    });
  }
}
