// Create the action listeners constant
const actionListeners = new Map();

/**
 * @class AppDispatcher
 * @description An action dispatcher for the state and store
 */
class AppDispatcher {
  /**
   * @private
   * @function _getActionListeners
   * @description Returns the list of action listeners
   * @returns {Object} action listeners
   */
  _getActionListeners() {
    return actionListeners;
  }

  /**
   * @function add
   * @description Adds a new action listener
   * @param {String} name - The action name constant
   * @param {Function} action - The action function
   * @returns {Boolean} True, if action is added else false
   */
  add(name, action) {
    if (typeof name !== 'string' || typeof action !== 'function') {
      console.warn('ActionDispatcher: Unable to add action: %o for action name: %o', action, name);
      return false;
    }

    if (!actionListeners.has(name)) {
      actionListeners.set(name, []);
    }

    const actions = actionListeners.get(name);
    if (actions.includes(action)) {
      console.warn('ActionDispatcher: action: %o is already present for action name: %o', action, name);
      return true;
    }

    try {
      actions.push(action);
      actionListeners.set(name, actions);
      return true;
    } catch (e) {
      console.warn('AppDispatcher: Unable to add action: %o for actionName: %o, stack: %o', action, name, e);
      return false;
    }
  }

  /**
   * @public
   * @function remove
   * @description Removes an action listener
   * @param {String} name - The action name constant
   * @param {Function} action - The action function
   * @returns {Boolean} True, if action is removed else false
   */
  remove(name, action) {
    if (typeof name !== 'string' || typeof action !== 'function') {
      console.warn('AppDispatcher: The action: %o to remove for action name: %o is not a function', action, name);
      return false;
    }

    const actions = actionListeners.get(name);

    if (!actions) {
      console.warn('AppDispatcher: Can\'t remove action: %o for actionName: %o not found', action, name);
      return false;
    }

    const index = actions.indexOf(action);
    if (index < 0) {
      console.warn('AppDispatcher: action: %o already removed for actionName: %o', action, name);
      return true;
    }

    try {
      actions.splice(index, 1);
      return true;
    } catch (e) {
      console.warn('AppDispatcher: Unable to remove action: %o for actionName: %o, stack: %o', action, name, e);
      return false;
    }
  }

  /**
   * @function executeActions
   * @description Executes all the actions registered for the given action name
   * @param {String} name - The action name constant
   * @param {Object} data - Data to pass to actions
   * @returns {Boolean} True, if all actions are executed, else false
   */
  executeActions(name, data, store) {
    const actions = actionListeners.get(name);
    if (!actions || actions.length < 1) {
      return false;
    }
    actions.forEach((action) => {
      try {
        action(data, store);
      } catch (e) {
        console.warn(`AppDispatcher: Unable to execute ${action}`);
      }
    });
    return true;
  }
}

const obj = new AppDispatcher();
export default obj;
