import { TemplateComponent, definition } from '@eui/component';
import template from './container.html';
import style from './container.css';
import { addSharedCSS } from '../../utils/General';

import Config from '../../core/Config';
import SystemBar from '../systemBar/SystemBar';
import LayoutHolder from '../layoutHolder/LayoutHolder';
import ErrorMessage from '../errorMessage/ErrorMessage';

import { AppStore, Localizer } from '../../util-helpers';
import {
  MENU_TOGGLE_CLICKED,
  APP_LOADING,
  APP_LOADED,
  APP_LOADING_FAILED,
  LOCALE_UPDATED,
} from '../../utils/Actions';

@definition('eui-container', {
  style,
  template: addSharedCSS(template),
  templatePosition: 'APPEND',
})
export default class Container extends TemplateComponent {
  constructor() {
    super();
    this._bindMethods();
    this._setupListeners();
  }

  /**
   * @function didConnect
   * @description Component is connected to the DOM
   */
  didConnect() {
    AppStore.dispatch(MENU_TOGGLE_CLICKED, Config.menu.openMenu);
    if (this._isIFrame()) {
      this.embedded = true;
      const layoutHolder = this.shadowRoot.querySelector('eui-container-layout-holder');
      layoutHolder.setAttribute('embedded', '');
    } else {
      SystemBar.register();
    }
  }

  /**
   * @private
   * @function _isIFrame
   * @description Returns true if the container is running in an iFrame
   */
  _isIFrame = () => window.location !== window.parent.location;

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this._onBeforeAppChange = this._onBeforeAppChange.bind(this);
    this._onAppChange = this._onAppChange.bind(this);
    this._onAppError = this._onAppError.bind(this);
  }

  /**
   * @private
   * @function _setupListeners
   * @description Sets up AppStore listeners to listen to events
   */
  _setupListeners() {
    AppStore.listen(APP_LOADING, this._onBeforeAppChange);
    AppStore.listen(APP_LOADED, this._onAppChange);
    AppStore.listen(APP_LOADING_FAILED, this._onAppError);
    AppStore.listen(LOCALE_UPDATED, () => {
      if (this.appTitle) {
        this._setDocumentTitle(this.appTitle);
      }
    });
  }

  /**
   * @private
   * @function _onBeforeAppChange
   * @description Handle the before change event and set the name displays
   * @param {String} data - Data For setting the document title
   */
  _onBeforeAppChange(data) {
    const { packageName } = data;
    const message = `${Localizer.loc.LOADING || 'Loading'}`;
    this._setDocumentTitle(`${message} ${packageName}`);
  }

  /**
   * @private
   * @function _onAppChange
   * @description Handles the app changed event and sets the new titles
   * @param {Object} data - The configuration file for the new application
   */
  _onAppChange(data) {
    const { config } = data;
    if (!config) {
      console.warn('Container: Invalid config: %o for setting document title', config);
      return;
    }
    // store the current app's title.
    this.appTitle = config.title;
    this._setDocumentTitle(config.title);
  }

  /**
   * @private
   * @function _onAppError
   * @description Handles the app load error event and updates the display
   * @param {Object} data - The default options
   */
  _onAppError(data) {
    const { errorData } = data;
    const { header } = errorData;
    if (!header) {
      console.warn('Container: Invalid header: %o for setting document title', header);
      return;
    }
    this._setDocumentTitle(header);
  }

  /**
   * @public
   * @function _setDocumentTitle
   * @description Sets the main html document title element
   * @param {String} title - The title value to set
   */
  _setDocumentTitle(title) {
    document.title = Localizer.loc[title] || title;
  }
}

Container.registerComponents([
  ErrorMessage,
  LayoutHolder,
]);
