import { TemplateComponent, definition } from '@eui/component';
import template from './systemPanel.html';
import style from './systemPanel.css';
import Config from '../../core/Config';
import Cache from '../../core/Cache';
import { addSharedCSS, findElements } from '../../utils/General';
import Functions from '../../utils/Functions';
import { AppStore, Localizer } from '../../util-helpers';
import {
  LOAD_SYSTEM_PANEL,
  RIGHT_PACKAGE_LOADED,
  TOGGLE_SYSTEM_PANEL,
  UPDATE_LOCALE,
} from '../../utils/Actions';

@definition('eui-system-panel', {
  style,
  template: addSharedCSS(template),
})
export default class SystemPanel extends TemplateComponent {
  /**
   * @function constructor
   * @description Constructor for the Setting Panel
   */
  constructor() {
    super();

    findElements(this, [
      { name: 'container', selector: '#SystemPanel-inner' },
      { name: 'loaderComponent', selector: '.loader' },
      { name: 'closeIcon', selector: '.closeicon' },
      { name: 'systemPanelTitle', selector: '.systempanel-title' },
    ]);

    this._bindMethods();
    this._setupListeners();
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this._loadSystemPanel = this._loadSystemPanel.bind(this);
    this._attachPanel = this._attachPanel.bind(this);
    this._updateStrings = this._updateStrings.bind(this);
  }

  /**
   * @private
   * @function _setupListeners
   * @description Sets up AppStore listeners to listen to events
   */
  _setupListeners() {
    AppStore.listen(LOAD_SYSTEM_PANEL, this._loadSystemPanel);
    AppStore.listen(RIGHT_PACKAGE_LOADED, this._attachPanel);
    AppStore.listen(UPDATE_LOCALE, this._updateStrings);

    this.closeIcon.addEventListener('click', () => {
      AppStore.dispatch(TOGGLE_SYSTEM_PANEL, !Config.private.isSystemOpen);
    });
  }

  /**
 * @private
 * @function _clearContents
 * @descirption Clears all the system panel children apart from the loader and the panel header
 */
  _clearContents() {
    // const childNodes = [...this.container.children];
    const childNodes = Array.from(this.container.children);
    childNodes.forEach((child) => {
      const { className } = child;
      if (className === 'loadercontainer' || className === 'systempanel-inner__top') {
        return;
      }
      child.remove();
    });
  }

  /**
   * @private
   * @function _loadSystemPanel
   * @description Executes the request to load the system panel component
   * @param {Object} data - The app/component to insert into the layer
   * @param {String} data.id - Id of the System bar component
   * @param {String} data.packageName - Name of the package to load
   */
  _loadSystemPanel(data) {
    const {
      id,
      packageName,
    } = data;
    const current = Cache.getCurrent('right');
    const toggle = (current === packageName) && (this.callerId === id);
    this.callerId = id;
    if (toggle) {
      AppStore.dispatch(TOGGLE_SYSTEM_PANEL, !Config.private.isSystemOpen);
      return;
    }

    this.loader(true);

    // Execute the load package function
    Functions.loadPackage({
      type: 'right',
      packageName,
      localeInfo: {
        localeToLoad: AppStore.getState('locale'),
        localeIds: Config.i18n.localeIds,
      },
      path: Config.panel.panelsPath,
    });
  }

  /**
   * @private
   * @function _updateStrings
   * @description Update system panel strings
   */
  _updateStrings() {
    this.systemPanelTitle.innerHTML = Localizer.loc.PANEL_TITLE || '';
  }

  /**
   * @private
   * @function componentDidConnect
   * @description Component did connect to the DOm
   */
  didConnect() {
    this.state = AppStore.connect(this, 'locale');
  }

  /**
   * @private
   * @function _stateChanged
   * @description Handles the new state
   */
  _stateChanged(state) {
    if (this.state.locale !== state.locale) {
      this._updateStrings();
    }
  }

  /**
   * @private
   * @function _attachPanel
   * @description Attach the panel to the container
   * @param {Object} panel - The panel instance to inject into the dom
   * @returns {Boolean} True, if the panel is attached else false
   */
  _attachPanel(panel) {
    if (!panel || typeof panel !== 'object' || Array.isArray(panel)) {
      console.warn('SystemPanel: Unable to attach panel: %o', panel);
      return false;
    }

    try {
      this._updateStrings();
      this._clearContents();
      this.container.appendChild(panel);
      AppStore.dispatch(TOGGLE_SYSTEM_PANEL, true);
      this.loader(false);
      return true;
    } catch (e) {
      console.warn('SystemPanel: Unable to attach panel : %o, stack: %o', panel, e);
      return false;
    }
  }

  /**
   * @function loader
   * @description Toggles the loader component in the system panel
   * @param {Boolean} display - True/False shows/hides the loader
   */
  loader(display) {
    this.loaderComponent.hidden = !display;
    if (!display) {
      this.container.className = 'hideloader';
      return;
    }

    this._clearContents();
    this.container.className = '';
  }
}
