const internal = new Map();
const packages = new Map();

// Current apps only references the "name" of the package not a "duplicate" instance
// reference which would be unnecessary
const currentPackages = {
  left: null,
  app: null,
  right: null,
};

/**
 * @class Cache
 * @description  Simple Cache Mechanism for the internal container components if
 * instantiated imperatively and also the App and Panel packages
 */
class Cache {
  /**
   * @public
   * @function storeInternal
   * @description Store the component reference in the cache, these are internal
   * components which formulate the container framework
   */
  storeInternal(name, component) {
    if (typeof name !== 'string' || typeof component !== 'object' || Array.isArray(component)) {
      console.warn('Cache: Unable to save reference name:% for component: %o', name, component);
    }
    internal.set(name, component);
  }

  /**
   * @public
   * @function storeComponent
   * @description Returns a cached internal component
   * @param {String} name - The name of the component to return
   */
  getInternal(name) {
    const component = internal.get(name);
    if (!component) {
      console.warn('Cache: Unable to retrieve component with name: %o', name);
      return null;
    }
    return component;
  }

  /**
   * @public
   * @function destroyInternal
   * @description Remove the component reference from the cache, this is seldom
   * used in the container as the components exist throughout the lifecycle of
   * the container instance
   */
  destroyInternal(name) {
    const isComponentRemoved = internal.delete(name);
    if (!isComponentRemoved) {
      console.warn('Cache: Unable to remove component with name: %o', name);
      return false;
    }
    return true;
  }

  /**
   * @public
   * @function storePackage
   * @description Store the app reference in the cache
   * type, packageName, packageInstance, config, isCurrentPackage = true, locationParams
   */
  storePackage(data) {
    const {
      type,
      packageName,
      packageInstance,
      config,
      isCurrentPackage,
      locationParams,
    } = data;
    try {
      const timeout = new Date();
      const packageData = {
        id: packageName,
        instance: packageInstance,
        config,
        locationParams,
        timeout,
      };
      packages.set(packageName, packageData);
      if (isCurrentPackage) { this.setCurrent(type, packageName); }
    } catch (e) {
      console.warn('Cache: Unable to store package with name: %o in cache, stack: %o', packageName, e);
    }
  }

  /**
   * @public
   * @function setCurrent
   * @description Sets a current panel or app
   * @param {String} type - Set currentPackage of the given type left/right/app
   * @param {String} name - Name of package to set
   */
  setCurrent(type, name) {
    if (typeof type !== 'string' || typeof name !== 'string') {
      console.warn('Cache: Unable to set current package with name: %o, in type: %o', name, type);
      return;
    }
    currentPackages[type] = name;
  }

  /**
   * @public
   * @function getCurrent
   * @description Returns the current package name for the designated section
   * @param {String} type - The type reference, left, app, right
   * @returns {Object} Current loaded package of type[left, app, right]
   */
  getCurrent(type) {
    if (typeof type !== 'string' || type === '') {
      console.warn('Cache: Unable to get current component of type: %o', type);
      return null;
    }
    return currentPackages[type];
  }

  /**
   * @public
   * @function getCurrentLoaded
   * @description Returns a formatted object with the current loaded app & packages
   * @returns {Object} Current loaded instance of type[left, app, right]
   */
  getCurrentLoaded() {
    return {
      left: { id: currentPackages.left },
      app: { id: currentPackages.app },
      right: { id: currentPackages.right },
    };
  }

  /**
   * @public
   * @function getCurrentInstance
   * @description Returns the current instance for the current selected section
   * @param {String} type - The type reference, left, app, right
   * @returns {object} Current loaded instance of given type
   */
  getCurrentInstance(type) {
    if (typeof type !== 'string') {
      console.warn('Cache: Unable to get current instance of type: %o', type);
      return null;
    }
    const current = packages.get(currentPackages[type]);
    if (!current) {
      // console.warn('Cache: No instances found for type: %o', type);
      return null;
    }
    return current.instance;
  }

  /**
   * @public
   * @function clearCurrent
   * @description Clears the current instance referenced for a specific type
   * @param {String} type - The type reference, left, app, right
   */
  clearCurrent(type) {
    if (typeof type !== 'string') {
      console.warn('Cache: Unable to clear current instance of type: %o', type);
      return;
    }
    currentPackages[type] = null;
  }

  /**
   * @public
   * @function destroyPackage
   * @description Remove the package reference from the cache
   */
  destroyPackage(name) {
    if (typeof name !== 'string') {
      console.warn('Cache: Unable to destroy package with name: %o', name);
      return;
    }
    // TODO: This could actually fire a "destroy" method on the app or panel
    // to clear any objects and speed up garbage collection
    packages.delete(name);
  }

  /**
   * @public
   * @function purge
   * @description Clear all loaded packages
   */
  purge() {
    try {
      packages.clear();
      currentPackages.left = null;
      currentPackages.app = null;
      currentPackages.right = null;
      return true;
    } catch (e) {
      console.warn('Cache: Unable to clear cached external components: %o', e);
      return false;
    }
  }

  /**
   * @public
   * @function isPackageCached
   * @description Check the package cache to determine if a package instance has
   * been cached and return the package object for reuse
   * @param {String} name - The name of the app package to check in the cache
   */
  isPackageCached(name) {
    if (!name) {
      console.warn('Cache: Unable to check if package with name: %o is cached or not', name);
      return null;
    }
    return packages.get(name);
  }

  /**
   * @public
   * @function checkCache
   * @description Check the current stored cached and deletes anything with a time
   * stamp older than the max age
   * @param {Number} maxAge - The maximum age for an item to be stored
  */
  checkCache(maxAge) {
    if (typeof maxAge !== 'number') {
      console.warn('Cache: Invalid maximum age: %o provided', maxAge);
      return;
    }
    const now = new Date();
    const age = maxAge * 1000 * 60;
    packages.forEach((item) => {
      const { timeout: packageUpdated, id: packageName } = item;
      if (packageName === currentPackages.left
        || packageName === currentPackages.right
        || packageName === currentPackages.app) {
        return;
      }
      const diff = now.getTime() - packageUpdated.getTime();
      if (diff >= age) {
        // Localizer.purge(key);
        packages.delete(item.id);
      }
    });
  }
}

const obj = new Cache();
export default obj;
