/* global fetch */

/**
 * @class A Standard Loader
 * @description  A Standard Loader for ajax requests using promises and fetch
 */
export class Loader {
  /**
   * @function json
   * @description Execute an ajax promise request
   * @param {String} filePath - The requested JSON file to load
   * @param {Function} callback - Optional callback
   */
  async json(filePath, callback = null) {
    if (typeof filePath !== 'string') {
      console.warn(`PromiseLoader: Invalid JSON file provided: ${filePath}`);
      return null;
    }

    try {
      const response = await fetch(`${filePath}${window.EUI_TS ? `?${window.EUI_TS}` : ''}`);
      if (!response.ok) {
        throw new Error(`PromiseLoader: ${response.status} Failed to fetch the JSON file ${response.url}`);
      }
      const json = await response.json();
      if (callback) {
        callback(json);
        return null;
      }
      return json;
    } catch (e) {
      console.warn(`PromiseLoader: Failed to fetch the JSON file ${filePath} %O`, e);
      return null;
    }
  }

   /**
   * @function run
   * @description Execute an ajax promise request
   * @param {String} path - The request Path
   * @param {Object} request - The request object containing the request details
   * @param {String} resType - The response format type
   * @param {Function} formatter - An optional formatter function for the APP
   * type, headers etc.
   * {
      type: 'POST',
      headers: {
        'content-type':'application/json; charset=UTF-8',
        'x-requested-with':'XMLHttpRequest'
      }
   * }
   */
  run(name, path, request, resType = 'json', formatter = null, callback = null) {
    return fetch(path, request)
    .then((response) => {
      if (response.status === 200) {
        if (resType === 'json') {
          return response.json();
        }
      }
      return response.text();
    }).then((data) => {
      let res = data;
      if (resType === 'json' && formatter) {
        res = formatter(data);
      }
      if (callback && typeof callback === 'function') {
        callback(name, data);
      }
      return res;
    }).catch((error) => {
      console.warn('Request Error: ', error);
    });
  }
}

const obj = new Loader();
export default obj;
