import { TemplateComponent, definition } from '@eui/component';
import Config from '../../core/Config';
import template from './systemBar.html';
import style from './systemBar.css';
import { addSharedCSS, findElements } from '../../utils/General';

import {
  AppStore,
  PackageLoader,
  Router,
} from '../../util-helpers';

import {
  ADD_SYS_COMPONENT,
  REMOVE_SYS_COMPONENT,
  LOCALE_UPDATED,
} from '../../utils/Actions';

@definition('eui-container-system-bar', {
  style,
  template: addSharedCSS(template),
})
export default class SystemBar extends TemplateComponent {
  /**
   * @function constructor
   * @description Constructor for the SystemBar
   */
  constructor() {
    super();

    // Object to cache the components in
    this.objects = {
      left: new Set(),
      right: new Set(),
    };

    // components will be added from left to right
    // on the basis of components order in the container config
    this.defaultRendering = true;

    findElements(this, [
      // { name: 'container', selector: '#SysBar-container' },
      { name: 'leftSlot', selector: '#left-slot' },
      { name: 'rightSlot', selector: '#right-slot' },
    ]);

    this._bindMethods();
    this._setupListeners();
    this._loadSystemBar();
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this._handleItemClick = this._handleItemClick.bind(this);
    this._addComponents = this._addComponents.bind(this);
    this._removeComponent = this._removeComponent.bind(this);
    this._setLocaleOnSystemBarComponents = this._setLocaleOnSystemBarComponents.bind(this);
  }

  /**
   * @private
   * @function _setupListeners
   * @description Sets up AppStore listeners to listen to events
   */
  _setupListeners() {
    // dynamically addd component to the SystemBar
    AppStore.listen(ADD_SYS_COMPONENT, (components) => {
      this.defaultRendering = false;
      this._addComponents(components);
    });
    AppStore.listen(REMOVE_SYS_COMPONENT, this._removeComponent);
    AppStore.listen(LOCALE_UPDATED, (data) => {
      this.locale = data.locale;
      this._setLocaleOnSystemBarComponents(this.locale);
    });
  }

  /**
   * Iterate over all added system bar components.
   * Set the locale prop an all found components.
   * Setting the locale prop on a component causes it to fetch it's locale strings.
   *
   * @function _setLocaleOnSystemBarComponents
   * @param {String} locale - locale to set ("en-us")
   */
  _setLocaleOnSystemBarComponents(locale) {
    const { leftSlot, rightSlot } = this;

    leftSlot.childNodes.forEach((leftComponent) => {
      leftComponent.locale = locale;
    });
    rightSlot.childNodes.forEach((rightComponent) => {
      rightComponent.locale = locale;
    });
  }

  /**
   * @private
   * @function _loadSystemBar
   * @description Loads the System bar
   */
  async _loadSystemBar() {
    const { ui } = Config;
    if (!ui || typeof ui !== 'object' || Array.isArray(ui)) {
      console.warn('AppNav: Unable to load System bar with \'ui\' config: %o', ui);
      return;
    }

    const { top: topComponents } = ui;
    if (!topComponents || !Array.isArray(topComponents)) {
      console.warn('AppNav: Unable to load SystemBar with \'ui.top\' config: %o', topComponents);
      return;
    }
    await this._addComponents(topComponents);
  }

  /**
   * @private
   * @function _addComponents
   * @description Adds components to System bar
   * @param {object/Array} components - Single component or list of components to add
   */
  async _addComponents(components) {
    if (!components) { return; }
    const isArray = Array.isArray(components);
    if (!isArray) {
      await this._loadComponent(components);
    } else if (components.length > 0) {
      await this._loadComponents(components);
    }
  }

  /**
   * @private
   * @function _loadComponent
   * @description Loads a component in System bar
   * @param {Object} component - component to load in System bar
   * @param {String} component.type - Type of component [button/package]
   * @param {String} component.position - Position [left/right] of component in the SystemBar
   * @param {String} component.id - Id of the component to load
   * @param {String} component.uiPath - path to components that come with container
   */
  async _loadComponent(component) {
    const componentIsCached = this._isCached(component);
    if (componentIsCached) {
      return null;
    }
    const {
      type, position, id, uiPath,
    } = component;
    let componentNode = null;
    if (type === 'button') {
      componentNode = this._createButton(component);
    } else if (type === 'package') {
      if (uiPath) {
        componentNode = await this._createComponent(component, uiPath);
      } else {
        componentNode = await this._createComponent(component, Config.component.componentsPath);
      }
    }
    componentNode.id = id;
    componentNode.position = position;
    componentNode.locale = this.locale || window.EUI.Localizer.defaultLocale;

    // cache the component
    this.objects[position].add(id);

    return componentNode;
  }

  /**
   * @private
   * @function _loadComponents
   * @description loads components in System bar
   * @param {Array} components - list of components to load
  */
  async _loadComponents(components) {
    const loadingPromises = components.map(component => new Promise((resolve) => {
      const componentNode = this._loadComponent(component);
      resolve(componentNode);
    }));

    await Promise.all(loadingPromises).then((componentNodes) => {
      componentNodes.forEach((componentNode) => {
        if (componentNode) {
          this._injectComponent(componentNode);
        }
      });
    });
  }

  /**
   * @private
   * @function _isCached
   * @description Checks if the component is cached
   * @param {Object} component - component to check if its cached
   * @returns {Boolean} true, if the component is cached else false
   */
  _isCached(component) {
    const { position, id } = component;
    const positionCache = this.objects[position];
    return (positionCache && positionCache.length < 1) || positionCache.has(id);
  }

  /**
   * @private
   * @function _createButton
   * @description Creates a standard button with an icon
   * @param {Object} data - The button data
   * @param {String} data.icon - Icon to load
   * @param {String} data.label - Label to set
   * @param {String} data.action - Action name to set
   * @param {String} data.payload - Payload to set
   * @param {String} data.route - Route to set
   * @returns {Object} button node object
   */
  _createButton(data) {
    const button = document.createElement('button');
    const {
      icon, label, action,
      payload, route,
    } = data;
    button.innerHTML = icon ? `<eui-v0-icon color="var(--white)" name="${icon}"></eui-v0-icon>` : label;
    button.classList.add('sys-nav-button');
    button.addEventListener('click', this._handleItemClick, true);
    if (action) { button.setAttribute('data-action', action); }
    if (payload) { button.setAttribute('data-payload', payload); }
    if (route) { button.setAttribute('data-route', route); }
    return button;
  }

  /**
   * @private
   * @function _createComponent
   * @description Creates a component to inject in System bar
   * @param {String} path - path to the component in the components folder
   * @param {String} action - action to be dispatched by the component
   * @param {Object} payload - payload for the action dispatch, if a panel,
   * then it should reflect the path name to the panel folder
   * @param {String} route - route to navigate to by dispatching action on the component
   * @param {String} componentsPath - path to the components folder
   */
  async _createComponent({
    path, action, payload, route, version,
  }, componentsPath) {
    let packageObj;

    // TODO: version is checked against to tell if it's loading a shareable component
    if (version) {
      packageObj = await PackageLoader._loadShareableComponent(
        `${componentsPath}${path}/${version.match(/\d+/g)[0]}`,
      );
    } else {
      packageObj = await PackageLoader.load({
        packageName: path,
        path: componentsPath,
      });
    }

    const { module, config } = packageObj;
    const Com = (typeof module === 'function') ? module : module.default;
    Com.register(Com.is);

    Object.assign(config, {
      onClick: this._handleItemClick,
      action,
      payload,
      route,
    });

    const com = new Com();
    com.options = config;
    if (this.dir === 'rtl') {
      com.dir = 'content-dir-rtl';
    }
    return com;
  }

  /**
   * @private
   * @function _injectComponent
   * @description Injects a component into the provided slots
   * @param {Object} componentNode - Component to inject into the slot
   */
  _injectComponent(componentNode) {
    const { leftSlot, rightSlot, defaultRendering } = this;
    const slots = {
      left: leftSlot,
      right: rightSlot,
    };

    const { position } = componentNode;
    let attach = 'appendChild';
    if (!defaultRendering && position === 'right') {
      attach = 'prepend';
    }
    slots[position][attach](componentNode);
  }

  /**
   * @private
   * @function _handleItemClick
   * @description Handles the button/component click event actions, this will either
   * dispatch an action through the dispatcher or alter the current route
   * @param {Object} e - Click event
   */
  _handleItemClick(e) {
    const target = e.currentTarget;
    const action = target.getAttribute('data-action');
    const packageName = target.getAttribute('data-payload');
    const route = target.getAttribute('data-route');
    if (action && packageName) {
      AppStore.dispatch(action, {
        id: target.id,
        packageName,
      });
    }
    if (route) {
      Router.goto(route);
    }
  }

  /**
   * @private
   * @function _removeComponent
   * @description Removes a component from the SystemBar
   * @param {Object} component - Component to remove
   * @param {String} component.id - Id of the component
   * @param {String} component.position - position of the component in the SystemBar left or right
   */
  _removeComponent(component) {
    const { id, position } = component;
    if (!id || !position) {
      console.warn('SystemBar: Unable to remove component with id: %o && position: %o', id, position);
      return;
    }

    const { leftSlot, rightSlot } = this;
    const slots = {
      left: leftSlot,
      right: rightSlot,
    };
    const slotNode = slots[position];
    if (!slotNode) {
      console.warn(`System bar slot not found for position: ${position}`);
      return;
    }
    const componentId = `#${id}`;
    const nodeToRemove = slotNode.querySelector(componentId);
    if (nodeToRemove) {
      nodeToRemove.remove();
      this.objects[position].delete(id);
    }
  }
}
