/**
 * @class LayoutHolder
 * @description The main layout holder component which controls the content
 * for the left, center and right panels
 */
import { TemplateComponent, definition } from '@eui/component';
import style from './layoutHolder.css';
import template from './layoutHolder.html';

import SystemPanel from '../systemPanel/SystemPanel';
import AppNav from '../appNav/AppNav';
import AppContent from '../appContent/AppContent';
import AppBar from '../appBar/AppBar';

import { addSharedCSS, findElements } from '../../utils/General';
import { AppStore } from '../../util-helpers';
import Config from '../../core/Config';
import {
  CLOSE_NAV_MENU,
  RESET_SCROLL,
  TOGGLE_NAV_MENU,
  TOGGLE_SYSTEM_PANEL,
} from '../../utils/Actions';

@definition('eui-container-layout-holder', {
  style,
  props: {
    embedded: { attribute: true, type: Boolean },
  },
  template: addSharedCSS(template),
})
export default class LayoutHolder extends TemplateComponent {
  /**
   * @function constructor
   * @description The constructor for the LayoutHolder
   */
  constructor() {
    super();

    findElements(this, {
      holder: '#LayoutHolder',
      appLevel: '#LayoutHolder-app-level',
      systemPanelContainer: '#LayoutHolder-settings',
      appBarContainer: '#Layout-appbar',
      appNavContainer: '#LayoutHolder-app-level-nav',
      appContentContainer: '#LayoutHolder-app-content',
    });

    this._bindMethods();
    this._setupListeners();
  }

  didUpgrade() {
    if (this.embedded) {
      const appBar = this.shadowRoot.querySelector('eui-app-bar');
      appBar.setAttribute('embedded', '');
    }
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this.toggleLeftMenu = this.toggleLeftMenu.bind(this);
    this.closeLeftMenu = this.closeLeftMenu.bind(this);
    this._resetScroll = this._resetScroll.bind(this);
    this._toggleSystemPanel = this._toggleSystemPanel.bind(this);
  }

  /**
   * @private
   * @function _setupListeners
   * @description Sets up listeners
   */
  _setupListeners() {
    AppStore.listen(RESET_SCROLL, this._resetScroll);
    AppStore.listen(TOGGLE_NAV_MENU, this.toggleLeftMenu);
    AppStore.listen(CLOSE_NAV_MENU, this.closeLeftMenu);
    AppStore.listen(TOGGLE_SYSTEM_PANEL, this._toggleSystemPanel);
  }

  /**
   * @private
   * @function _resetScroll
   * @description Resets the content page scrollbar to the top
   */
  _resetScroll() {
    this.appContentContainer.scrollTop = 0;
  }

  /**
   * @private
   * @function _toggleSystemPanel
   * @description Toggles the system panel
   * @param {Boolean} isOpen - True/False, opens/closes the panel
   */
  _toggleSystemPanel(isOpen) {
    if (typeof isOpen !== 'boolean') {
      console.warn('LayoutHolder: Unable to toggle system panel with option: %o', isOpen);
      return;
    }
    // Set the requested open state
    Config.private.isSystemOpen = isOpen;
    if (!isOpen) {
      this.systemPanelContainer.classList.add('hidden');
      this.appLevel.classList.remove('slide-left');
      this.appLevel.classList.add('slide-right');
      return;
    }
    this.systemPanelContainer.classList.remove('hidden');
    this.appLevel.classList.add('slide-left');
    this.appLevel.classList.remove('slide-right');
  }

  /**
   * @public
   * @function toggleLeftMenu
   * @description Toggles the left menu for apps
   * @param {Boolean} isOpen - True/False, opens/closes the left menu
   */
  toggleLeftMenu(isOpen) {
    if (typeof isOpen !== 'boolean') {
      console.warn('LayoutHolder: Unable to toggle left menu with option: %o', isOpen);
      return;
    }
    this.holder.className = isOpen ? '' : 'menu-hidden';
  }

  /**
   * @public
   * @function closeLeftMenu
   * @description Forces a close on the left menu
   */
  closeLeftMenu() {
    this.holder.classList.add('menu-hidden');
  }
}

LayoutHolder.registerComponents([
  AppNav,
  SystemPanel,
  AppBar,
  AppContent,
]);
