import AppDispatcher from './AppDispatcher';

const store = new Map();
const observers = {};

/**
 * @class AppStore
 * @description  Simple App store, for storing state data on a global level
 */
class AppStore {
  /**
   * @function constructor
   * @description Constructor for the AppStore
   */
  constructor() {
    this._bindMethods();
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this.initialState = this.initialState.bind(this);
    this.setState = this.setState.bind(this);
    this.deleteState = this.deleteState.bind(this);
    this.getState = this.getState.bind(this);
    this.dispatch = this.dispatch.bind(this);
    this.connect = this.connect.bind(this);
    this.listen = this.listen.bind(this);
    this.unlisten = this.unlisten.bind(this);
  }

  /**
   * @private
   * @function _getStore
   * @description Returns the store
   * @returns {Object} Store
   */
  _getStore() {
    return store;
  }

  /**
   * @private
   * @function Returns the observers
   * @returns {Object} Observers
   */
  _getObservers() {
    return observers;
  }

  /**
  * @public
  * @function initialState
  * @description The initial state for the application store
  * @param {Object} state - The default state object
  */
  initialState(state = {}) {
    this._updateStore(state);
  }

  /**
   * @private
   * @function _updateStore
   * @description Updates the store with a state
   * @param {Object} state - State to update the store with
   */
  _updateStore(state) {
    if (!state || typeof state !== 'object' || Array.isArray(state)) {
      console.warn('AppStore: No state: %o provided to update the store', state);
      return;
    }
    Object.keys(state).forEach((key) => {
      store.set(key, state[key]);
    });
  }

  /**
   * @public
   * @function setState
   * @description Set a key value in the Store
   * @param {String/object} state - The key names to add to the store
   * @param {String/Number/Object} value - The value to save
   */
  setState(state, value) {
    if (typeof state === 'string') {
      store.set(state, value);
      this._notifyObservers({ [state]: value });
    } else {
      this._updateStore(state);
      this._notifyObservers(state);
    }
  }

  /**
   * @private
   * @function _notifyObservers
   * @description Check observers and notify them of any state changes
   * @param state - State changes to notify the observers
   */
  _notifyObservers(state) {
    if (!state || Array.isArray(state)) {
      console.warn('AppStore: Invalid state: %o provided to notify the observers', state);
      return;
    }

    const componentsToNotify = [];

    Object.keys(state).forEach((key) => {
      const components = observers[key];
      if (!components || !Array.isArray(components)) { return; }
      components.forEach((component) => {
        if (!componentsToNotify.includes(component)) {
          component.__states__ = {};
          componentsToNotify.push(component);
        }
        component.__states__[key] = state[key];
      });
    });

    componentsToNotify.forEach((component) => {
      component._stateChanged(component.__states__);
      delete component.__states__;
    });
  }

  /**
   * @public
   * @function deleteState
   * @description Delete a state from the store and removes the observers
   * @param {String} key - The state to remove from the store
   */
  deleteState(key) {
    store.delete(key);
    delete observers[key];
  }

  /**
   * @public
   * @function getState
   * @description Get the value of the state from the Store
   * @param {String} key - The key name to retrieve the store
   * @returns {String/Number/Object} value - Value of the state
   */
  getState(key) {
    if (typeof key !== 'string') {
      console.warn('AppStore: No value found for state: %o', key);
      return null;
    }
    return store.get(key);
  }

  /**
   * @public
   * @function dispatch
   * @description An Action Dispatcher for pre store modifications
   * @param {String} action - The action being requested
   * @param {Object} data - Data to dispatch
   */
  dispatch(action, data) {
    const isAction = AppDispatcher.executeActions(action, data, this);

    // this block might not be needed
    if (!isAction && data) {
      this.setState(data);
    }
  }

  /**
   * @public
   * @function listen
   * @description Sets an action listener
   * @param {String} name - The name of the action to listen to
   * @param {Function} func - The callback function to execute for the action
   */
  listen(name, func) {
    AppDispatcher.add(name, func);
  }

  /**
   * @public
   * @function unlisten
   * @description Removes an action listener
   * @param {String} name - The name of the action to listen to
   * @param {Function} func - The listener to remove
   */
  unlisten(name, func) {
    AppDispatcher.remove(name, func);
  }

  /**
   * @public
   * @function disconnect
   * @description Disconnects a component from the store, should be done before destroy
   * @param {Object} component - The component to disconnect from the store
   * @param {Object} state - State keys to disconnect from
   */
  disconnect(component, state) {
    if (!component || typeof component !== 'object' || Array.isArray(component)) {
      console.warn('AppStore: Failed to disconnect component: %o with state: %o from the store', component, state);
      return false;
    }

    if (typeof state === 'string') {
      return this._disconnectState(component, state);
    }

    return Object.keys(state).every(key => this._disconnectState(component, key));
  }

  /**
   * @private
   * @function _disconnectState
   * @param {Object} component - The component to disconnect from the store
   * @param {String} state - State key to disconnect from
   * @returns {Boolean} True, if component is disconnected from state changes else false
   */
  _disconnectState(component, state) {
    if (!component || typeof component !== 'object' || Array.isArray(component)
      || typeof state !== 'string' || state === '') {
      console.warn('AppStore: Failed to disconnect component: %o with state: %o from the store', component, state);
      return false;
    }

    const components = observers[state];
    if (!components) { return false; }

    const index = components.indexOf(component);
    if (index > -1) {
      components.splice(index, 1);
      return true;
    }

    return false;
  }

  /**
   * @public
   * @function connect
   * @description Connects a component to the store as an observer of specified
   * state changes and updates
   * @param {Object} component - The component to connect to the store
   * @param {String/Array} state - The key name to retrieve the store
   * @returns {Object} state values
   */
  connect(component, state) {
    if (typeof state === 'string') {
      return this._connectState(component, state);
    }

    if (!Array.isArray(state)) {
      console.warn('AppStore: Failed to connect component: %o with state: %o to the store', component, state);
      return { [state]: null };
    }

    const stateValues = {};
    for (const key of state) {
      const { [key]: value } = this._connectState(component, key);
      stateValues[key] = value;
    }

    return stateValues;
  }

  /**
   * @private
   * @function _connectState
   * @description
   * @param {Object} component - Component to connect
   * @param {String} state - key of the state to retrieve
   * @returns {Object} {key:value} of the state
   */
  _connectState(component, state) {
    if (!component || typeof component !== 'object' || Array.isArray(component) || typeof state !== 'string') {
      console.warn('AppStore: Failed to connect component: %o to the state: %o', component, state);
      return { [state]: null };
    }

    this._pushObserversForStateChanges(component, state);

    return { [state]: this.getState(state) };
  }

  /**
   * @private
   * @function _pushObserversForStateChanges
   * @param {Object} component - Component to notify of state changes
   * @param {String} state - State to observe
   */
  _pushObserversForStateChanges(component, state) {
    if (typeof state !== 'string' || typeof component !== 'object' || Array.isArray(component)) {
      console.warn('AppStore: Unable to observe state: %o changes for component: %o', state, component);
      return;
    }

    const isKeyPresentInObserver = Object.prototype.hasOwnProperty.call(observers, state);
    if (!isKeyPresentInObserver) {
      observers[state] = [];
    }

    if (!observers[state].includes(component)) {
      observers[state].push(component);
    }
  }
}

const obj = new AppStore();
export default obj;
