import { TemplateComponent, definition } from '@eui/component';
import template from './errorMessage.html';
import style from './errorMessage.css';
import { findElements } from '../../utils/General';

@definition(
  'eui-container-error-message',
  { style, template },
)
export default class ErrorMessage extends TemplateComponent {
  /**
   * @function constructor
   * @description Constructor for the ErrorMessage
   */
  constructor() {
    super();

    findElements(this, [
      { name: 'wrapper', selector: '.eaContainer-ErrorScreen' },
      { name: 'header', selector: '.eaContainer-ErrorScreen-header-title' },
      { name: 'content', selector: '.eaContainer-ErrorScreen-body' },
    ]);
  }

  /**
   * @public
   * @function show
   * @description Shows the error message
   * @param {Object} options - The options for the message
   * @param {String} options.header - Header for the error message
   * @param {String} options.content - Content for the error message
   */
  show(options) {
    const { header, content } = options;
    this.wrapper.removeAttribute('hide');
    this.header.textContent = header;
    this.content.textContent = content;
  }

  /**
   * @public
   * @function hide
   * @description Hides the error message
   */
  hide() {
    this.wrapper.setAttribute('hide', 'hide');
  }
}
