/**
 * @function checkBrowserLocale
 * @param {Object} config - i18n config
 * @returns {String} Browser locale
 */
function checkBrowserLocale(i18nConfig) {
  const { defaultLocale, localeIds } = i18nConfig;
  // return if defaultLocale or list of supported locale Ids are not provided
  if (typeof defaultLocale !== 'string' || defaultLocale === ''
    || !Array.isArray(localeIds) || localeIds.length < 1) {
    console.warn('Browser: Unable to check browser locale with Default locale: %o and locale ids: %o', defaultLocale, localeIds);
    return null;
  }

  const _browserLocale = (window.navigator.systemLanguage
    || window.navigator.browserLanguage
    || window.navigator.language).toLowerCase();
  const _defaultLocale = defaultLocale.toLowerCase();

  const lowercaseLocaleIds = new Set(localeIds.map(locale => locale.toLowerCase()));
  // locale set in the browser takes priority
  if (lowercaseLocaleIds.has(_browserLocale)) {
    return _browserLocale;
  }
  return _defaultLocale;
}

export default {
  checkBrowserLocale,
};
