import { TemplateComponent, definition } from '@eui/component';
import template from './appContent.html';
import style from './appContent.css';
import Config from '../../core/Config';
import ErrorMessage from '../errorMessage/ErrorMessage';
import { addSharedCSS, findElements } from '../../utils/General';
import {
  APP_LOADING,
  APP_LOADED,
  APP_LOADING_FAILED,
} from '../../utils/Actions';
import { AppStore } from '../../util-helpers';

@definition('eui-app-content', {
  style,
  template: addSharedCSS(template),
})
export default class AppContent extends TemplateComponent {
  /**
   * @function constructor
   * @description The constructor function for the AppContent
   * @param {Object} options - The options for the AppContent object, this may
   * include the event bus if required
   */
  constructor() {
    super();

    this.errorMessage = null;
    this.loaderObj = null;

    findElements(this, [
      { name: 'innerContent', selector: '#AppContent-inner' },
      { name: 'loaderComponent', selector: '.loader' },
    ]);

    this._bindMethods();
    this._setupListeners();
    this._setPadding();
  }

  /**
   * @private
   * @function _bindMethods
   * @description Binds methods to 'this' context
   */
  _bindMethods() {
    this.loader = this.loader.bind(this);
    this.attachApp = this.attachApp.bind(this);
    this.removeApp = this.removeApp.bind(this);
    this.showError = this.showError.bind(this);
  }

  /**
   * @private
   * @function _setUpListeners
   * @description Sets up listeners
   */
  _setupListeners() {
    AppStore.listen(APP_LOADING, this.loader);
    AppStore.listen(APP_LOADED, this.attachApp);
    AppStore.listen(APP_LOADING_FAILED, this.showError);
  }

  /**
   * @private
   * @function _setPadding
   * @description This feature adds or removes the inner padding from the main app
   * container, this is required if the content of the app is to be
   * flush with the edges. An example being the Splash screen of the
   * E-UI Docs
   */
  _setPadding() {
    const { content } = Config;
    if (content && !content.padding) {
      this.innerContent.classList.add('no-padding');
    }
  }

  /**
   * @public
   * @function attachApp
   * @description Attaches the app to the container
   * @param {Object} data Data for attaching app
   * @returns {Boolean} True, if the app is attached else false
   */
  attachApp(data) {
    const { app } = data;
    try {
      this._clearAppContents();
      this.innerContent.appendChild(app);
      this.loader(false);
    } catch (e) {
      console.warn('AppContent: Unable to attach app: %o, stack: %o', app, e);
    }
  }

  /**
   * @public
   * @function removeApp
   * @description Removes the current loaded app from the display
   * @param {Object} app The app to remove from the App Content
   * @returns {Boolean} True, if the app is removed else false
   */
  removeApp(app) {
    try {
      app.remove();
    } catch (e) {
      console.warn('AppContent: Unable to remove app: %o, stack: %o', app, e);
    }
  }

  /**
   * @private
   * @function _clearAppContents
   * @description Clears all the app content children apart from the loader
   */
  _clearAppContents() {
    const childNodes = Array.from(this.innerContent.children);
    childNodes.forEach((child) => {
      if (child.className !== 'loadercontainer') {
        if (child.remove) {
          child.remove();
        }
      }
    });
  }

  /**
   * @function loader
   * @description Toggles the loader component in the App content
   * @param {Boolean} display - True/False, shows/hides the loader
   */
  loader(display = true) {
    this.loaderComponent.hidden = !display;

    if (!display) {
      this.innerContent.classList.add('hideloader');
      return;
    }

    this._clearAppContents();
    this.innerContent.classList.remove('hideloader');
  }

  /**
   * @public
   * @function showError
   * @description Toggles the error display
   * @param {Object} data - Data for showing error
   * @param {Boolean} show - State of the display, true/false for showing and hiding
   */
  showError(data, show = true) {
    const { errorData } = data;
    this._clearAppContents();
    if (show) {
      this.errorMessage = new ErrorMessage();
      this.errorMessage.show(errorData);
      this.innerContent.appendChild(this.errorMessage);
      this.loader(false);
      return;
    }
    if (this.errorMessage) {
      this.errorMessage.remove();
      this.errorMessage = null;
    }
  }
}
